import React, { useState } from "react";
import { Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { signup } from '../actions/auth';
import bg_login_img from '../static/images/bg-01.jpg';
const Signup = ({ signup, isAuthenticated }) => {

    const [accountCreated, setAccountCreated] = useState(false)
    const $ = document.querySelector.bind(document);

    const [formData, setFormData] = useState({
        username: '',
        email: '',
        password: '',
        re_password: ''
    });
    const [errors, setErrors] = useState([])

    const { username, email, password, re_password } = formData;
    const onChange = e => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    }
    const onSubmit = e => {
        e.preventDefault();
        if (password === re_password) {
            signup(username, email, password, re_password);
            setAccountCreated(true)
        }
    };

    const onBlur = (e) => {
        const errorMessage = $('.wrapper--logger')
        const loginForm = $("#signup-form")
        const label = loginForm.querySelector(`label[for="${e.target.name}"]`)
        const fields = loginForm.querySelectorAll('input.input-field')


        if (e.target.value === '') {
            if (!(errors.includes(`${label.textContent}`))) {
                setErrors(prev => [...prev, `${label.textContent}`])
            }
        }
        else {
            if ((errors.includes(`${label.textContent}`))) {
                setErrors(prevs => [...(prevs.filter(prev => prev !== `${label.textContent}`))])
            }
        }

    }

    // is the user authenticated?
    // change redirect them to the home page

    if (isAuthenticated) {
        return <Navigate to="/card" />
    }
    if (accountCreated) {
        return <Navigate to="/login" />
    }

    return (
        <div id="signup" className="wrapper--signup">
            <div className="signup--container" style={{ background: `url(${bg_login_img}) center / cover ` }}>
                <div className="sign--form validate-form">
                    <h1 className="sign--form__title">
                        
                    </h1>
                    <span className="wrapper--logger">
                        {errors && errors.map((error, i) => (<span key={i}>{error}</span>))}
                    </span>
                    <form method="post"
                        style={{ width: '100%' }}
                        onSubmit={e => onSubmit(e)}
                        id="signup-form"
                    >
                        <div className="wrapper--input validate-input">
                            <label className="input--label input--label__username" htmlFor="username"></label>
                            <div className="wrapper--fields wrapper--fields__username">

                                <i className="signup--form__icon fa-regular fa-user"></i>
                                <input className="signup--input__fields input-field input__username"
                                    type="text"
                                    id="username"
                                    name="username"
                                    value={username}
                                    onChange={e => onChange(e)}
                                    onBlur={onBlur}
                                    required
                                    placeholder=""
                                />
                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className="wrapper--input validate-input">
                            <label className="input--label input--label__email" htmlFor="email"></label>
                            <div className="wrapper--fields wrapper--fields__email">
                                <i className="signup--form__icon fa-regular fa-envelope"></i>
                                <input className="signup--input__fields input-field input__password"
                                    type="email"
                                    id="email"
                                    name="email"
                                    value={email}
                                    onChange={e => onChange(e)}
                                    onBlur={onBlur}
                                    required
                                    placeholder="Type your email" />
                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className=" wrapper--input validate-input">
                            <label className="input--label input--label__password" htmlFor="password"></label>
                            <div className="wrapper--fields wrapper--fields__username">
                                <i className="signup--form__icon fa-solid fa-lock"></i>
                                <input className="signup--input__fields input-field input__password"
                                    type="password"
                                    id="password"
                                    name="password"
                                    value={password}
                                    onChange={e => onChange(e)}
                                    onBlur={onBlur}
                                    placeholder="Type your password"
                                    required
                                />
                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className=" wrapper--input validate-input">
                            <label className="input--label input--label__password" htmlFor="re_password"></label>
                            <div className="wrapper--fields wrapper--fields__username">
                                <i className="signup--form__icon fa-solid fa-lock"></i>
                                <input className="signup--input__fields input-field input__password"
                                    type="password"
                                    id="re_password"
                                    name="re_password"
                                    value={re_password}
                                    onChange={e => onChange(e)}
                                    onBlur={onBlur}
                                    required
                                    placeholder="Type your password" />
                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className="wrapper--btn">
                            <div className="btn--container">
                                <div className="signup--form__policy">
                                    <input type="checkbox" />
                                    <a className="signup--form__link"></a>

                                    <button className={`btn btn--signup btn--primary__bg ${errors ? 'invalid' : ''}`} type="submit">
                                        
                                    </button>
                                </div>
                            </div>
                        </div></form>
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
})

export default connect(mapStateToProps, { signup })(Signup);