import React, { useState, useEffect } from "react";
import { connect } from 'react-redux';
import {
    load_school_group,
    get_all_teacher_avatar,
    signup_teacher,
    get_all_teacher_account,
    update_user_profile_avatar,
    get_all_teachers,
    remove_teacher_account,
    update_teacher_profile
} from '../actions/auth';

import defaultUser from '../static/images/default_user.jpeg'


function Teachermanagement({
    signup_teacher,
    update_user_profile_avatar,
    get_all_teacher_account,
    teacher_list,
    school_groups,
    load_school_group,
    get_all_teachers,
    teacher_accounts,
    remove_teacher_account,
    get_all_teacher_avatar,
    teacher_avt_lst,
    update_teacher_profile }) {
    const $ = document.querySelector.bind(document);

    const [mounted, setMounted] = useState(false);
    const [edit, setEdit] = useState(false);

    const [add, setAdd] = useState(false);
    const [send, setSend] = useState(false);
    const [item, setItem] = useState({
        fullname: "",
        username: "",
        phone: "",
        address: "",
        email: "",
        date_of_birth: "",
        teacher_library: false,
        teacher_belong_to_name: "",
        teacher_course: "",
        teacher_sex: "",
        teacher_belong_to_id: ""
    });
    const [errors, setErrors] = useState([]);
    const sex = ['', '', ''];

    const {
        teacher_avt,
        fullname,
        username,
        phone,
        address,
        email,
        date_of_birth,
        teacher_library,
        teacher_course,
        teacher_sex,
        teacher_belong_to_id
    } = item;

    const [avatar, setAvatar] = useState();
    const [pic, setPic] = useState();

    function handleCloseModal() {
        const group = $(".group-modal");
        group.classList.remove("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200)
    };

    function handleOpenModal() {
        const group = $(".group-modal");
        group.classList.add("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200);
    };
    useEffect(() => {
        return () => {
            avatar && URL.revokeObjectURL(avatar.preview);
        }
    }, [avatar]);

    useEffect(() => {
        get_all_teacher_account();
        get_all_teacher_avatar();
        load_school_group();
        get_all_teachers();
    }, []);

    const handleAddItem = () => {
        handleOpenModal();
        setAdd(true);
        setEdit(true);
        setItem({
            fullname: "",
            username: "",
            phone: "",
            address: "",
            email: "",
            date_of_birth: "",
            teacher_library: false,
            teacher_belong_to_name: "",
            teacher_course: "",
            teacher_sex: "",
            teacher_belong_to_id: "ID"
        });
    };
    console.log(teacher_avt_lst)
    const handleDeleteItem = (teacher) => {
        let pk = teacher.user;
        remove_teacher_account(pk);
        setSend(true);
    }

    const handleUpdateItem = (item) => {
        setAdd(false);
        setEdit(true);
        setItem(item);
        setAvatar();

    }

    const handleItemOpenItem = (group) => {
        handleOpenModal();
        setEdit(false);
        setItem(group);

    };

    useEffect(() => {
        teacher_avt_lst.map(i => {
            if (i.user === item.user) {
                setPic(i.avatar);
            };
        })
    }, [item]);

    const onChange = e => {
        if (e.target.type === 'checkbox') {
            setItem({ ...item, [e.target.name]: JSON.parse(e.target.checked) });
        }
        else {
            setItem({ ...item, [e.target.name]: e.target.value });
        }
    };

    const handleChangeAvatar = e => {
        const file = e.target.files[0];
        file.preview = URL.createObjectURL(file);
        setAvatar(file);
        setPic();
    };


    const onSubmit = e => {
        e.preventDefault();

        if (add) {
            signup_teacher(
                fullname,
                phone,
                address,
                email,
                date_of_birth,
                teacher_library,
                teacher_course,
                teacher_sex,
                teacher_belong_to_id,
            );
            setSend(!send);
        }

        else {
            update_teacher_profile(fullname,
                phone,
                address,
                email,
                date_of_birth,
                teacher_library,
                teacher_course,
                teacher_sex,
                teacher_belong_to_id, item.user);
            setSend(!send);
            if (avatar) {
                update_user_profile_avatar(avatar, item.user);
            }
        }

    }
    if (send) {
        setTimeout(() => {
            handleCloseModal();
        }, 200);
        setSend(!send);
    }

    return (
        <div className="wrapper--group__container">
            <div className="header--groupmanagement">
                <h1 className="container--title"></h1>
                <div className="groupmanagement__controller">
                    <i className="group--icon group--add fa-solid fa-plus" onClick={handleAddItem}></i>
                </div>
            </div>
            <ul className="teacher--list">
                {teacher_list.map((group, index) => {

                    return (
                        <li key={index} className="teacher--items"
                            onClick={
                                () => handleItemOpenItem(group)
                            }>
                            <div className="teacher--description">
                                <h3>{group.fullname}</h3>
                                <span><b>{group.teacher_belong_to_name}</b></span>

                                {teacher_accounts.map((teacheraccount, i) => {
                                    if (teacheraccount.id === group.user) {
                                        return (
                                            <span key={i}>ID<b>{teacheraccount.username}</b></span>
                                        )
                                    }
                                }
                                )}
                                <span><b>{group.date_of_birth}</b></span>
                                <span><b>{group.phone}</b></span>
                            </div>
                        </li>
                    )
                })}
            </ul>
            <div className={`group-modal ${mounted ? 'open' : ""}`}>
                <div className="modal--contain">
                    <div className="modal--contain__header">
                        {add ? <h1></h1> :
                            <h1>{edit ? '' : ''}</h1>}
                        <i className="group--icon group--remove fa-solid fa-xmark" onClick={handleCloseModal}></i>
                    </div>
                    {errors && <div className="wrapper--logger">
                    </div>}

                    {edit ? (
                        <form method="post" className="group-form" onSubmit={e => onSubmit(e)}>
                            {!add &&

                                <div className="wrapper--input">

                                    <div style={{ height: "110px", }}>
                                        {
                                            avatar ? (

                                                <img src={avatar.preview}
                                                    style={{ width: "80px", position: "absolute" }}
                                                />
                                            ) :
                                                // }{
                                                pic &&
                                                <img src={pic}
                                                    style={{ width: "80px", position: "absolute" }}
                                                />
                                        }
                                    </div>

                                    <label className="avt-input">
                                        <input
                                            type="file"
                                            placeholder="ok"
                                            onChange={handleChangeAvatar}
                                            value={teacher_avt}
                                            style={{ flexShrink: 0 }}
                                        />
                                        
                                    </label>
                                </div>}
                            <div className="wrapper--input">
                                <label className="input--label"
                                    htmlFor="teacher_belong_to_id">ID
                                </label>
                                <select onChange={(e) => onChange(e)} id="teacher_belong_to_id" className="teacher_belong_to_id" name="teacher_belong_to_id" defaultValue={teacher_belong_to_id}>
                                    <option key="select" value={"ID"} >ID</option >
                                    {school_groups.map((item, index) => {
                                        return (
                                            <option name="teacher_belong_to_id" key={index} value={item.group_id}>{item.group_id}</option >
                                        )
                                    }
                                    )}
                                </select>
                            </div>
                            <div className="wrapper--input">
                                <label className="input--label"
                                    htmlFor="fullname">
                                </label>
                                <input className="input__fields"
                                    type='text'
                                    id="fullname"
                                    name="fullname"
                                    placeholder=""
                                    value={fullname}
                                    onChange={e => onChange(e)}
                                    maxLength={255}
                                    required
                                />
                            </div>
                            <div className="wrapper--input">
                                <label className="input--label"
                                    htmlFor="phone">
                                </label>
                                <input className="input__fields"
                                    type='tel'
                                    id="phone"
                                    name="phone"
                                    placeholder=""
                                    value={phone}
                                    onChange={e => onChange(e)}
                                    maxLength={255}
                                    required
                                />
                            </div>
                            <div className="wrapper--input">
                                <label className="input--label"
                                    htmlFor="address">
                                </label>
                                <input className="input__fields"
                                    type='text'
                                    id="address"
                                    name="address"
                                    placeholder=""
                                    value={address}
                                    onChange={e => onChange(e)}
                                    required
                                    maxLength={255}
                                />
                            </div>
                            <div className="wrapper--input">
                                <label className="input--label"
                                    htmlFor="teacher_email">
                                </label>
                                <input className="input__fields"
                                    type='email'
                                    id="email"
                                    name="email"
                                    placeholder=""
                                    value={email}
                                    onChange={e => onChange(e)}
                                    maxLength={255}
                                    required
                                />
                            </div>
                            <div className="wrapper--input">
                                <label className="input--label"
                                    htmlFor="date_of_birth">
                                </label>
                                <input className="input__fields"
                                    type='date'
                                    id="date_of_birth"
                                    name="date_of_birth"
                                    placeholder=""
                                    value={date_of_birth}
                                    onChange={e => onChange(e)}
                                    required
                                />
                            </div>
                            <div className="wrapper--input  checkbox_library">
                                <label className="input--label"
                                    htmlFor="teacher_library">
                                </label>
                                <input className="input__fields"
                                    type='checkbox'
                                    id="teacher_library"
                                    name="teacher_library"
                                    value={teacher_library}
                                    checked={teacher_library}

                                    onChange={(e) => onChange(e)}
                                />
                            </div>
                            <div className="wrapper--input">
                                <label className="input--label"
                                    htmlFor="teacher_course">
                                </label>
                                <input className="input__fields"
                                    type='text'
                                    id="teacher_course"
                                    name="teacher_course"
                                    placeholder=""
                                    value={teacher_course}
                                    checked={teacher_course}
                                    onChange={e => onChange(e)}
                                    maxLength={255}
                                    required
                                />
                            </div>
                            <div className="wrapper--input radio_sex">
                                <label className="input--label"
                                    htmlFor="teacher_sex">
                                </label>
                                <input className="input__fields"
                                    type='radio'
                                    id="teacher_sex"
                                    name="teacher_sex"
                                    value="2"
                                    checked={teacher_sex === "2"}
                                    onChange={e => onChange(e)}
                                    required
                                />
                                <p>
                                    
                                </p>
                                <input className="input__fields"
                                    type='radio'
                                    id="teacher_sex"
                                    name="teacher_sex"
                                    value="1"
                                    checked={teacher_sex === "1"}
                                    onChange={e => onChange(e)}
                                    required
                                />
                                <p>

                                    
                                </p>
                                <input className="input__fields"
                                    type='radio'
                                    id="teacher_sex"
                                    name="teacher_sex"
                                    value="3"
                                    checked={teacher_sex === "3"}
                                    onChange={e => onChange(e)}
                                    required
                                />
                                <p>
                                    
                                </p>
                            </div>


                            <button className="btn group--btn primary" type="submit">{edit ? "" : ""}</button>
                        </form>
                    ) : (
                        <div className="teacher--infor__container">
                            <div className="user--header">
                                <div className="user--description">
                                    {

                                        <img src={pic}
                                            style={{ width: "80px" }}
                                            onError={(e) => { e.target.src = defaultUser }}
                                        />
                                    }
                                    <h2 className="teacher--infor">
                                        <span>{item.fullname}</span><br />
                                        <span>{item.username}</span>
                                    </h2>
                                </div>
                                <div className="user--controller">
                                    <button className="user--controller__edit" onClick={() => handleUpdateItem(item)} type="submit"></button>
                                    <button className="user--controller__remove" type="submit" onClick={() => handleDeleteItem(item)}></button>
                                </div>
                            </div>
                            <ul className="teacher--infor__list">
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.teacher_belong_to_id}</p>
                                </li>
                                {teacher_accounts.map((account, index) => {
                                    if (item.user === account.id) {
                                        item.username = account.username;
                                        item.email = account.email
                                        return (
                                            <li key={index} className="teacher--infor__item">
                                                <span><span></span></span>
                                                <p >{account.email}</p>
                                            </li>
                                        )
                                    }

                                })}
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.phone}</p>
                                </li>

                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.address}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.date_of_birth}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.teacher_course}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{sex[item.teacher_sex - 1]}</p>
                                </li>
                            </ul>
                        </div>
                    )}
                </div>
                <div className="modal--group__bg" onClick={handleCloseModal}></div>
            </div >
        </div >
    )
}
const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    // teacher: state.auth.teacher,
    teacher_list: state.auth.teacher_list,
    school_groups: state.auth.school_groups,
    teacher_accounts: state.auth.teacher_accounts,
    teacher_avt_lst: state.auth.teacher_avt_lst
})
export default connect(mapStateToProps, {
    update_teacher_profile,
    get_all_teacher_avatar,
    load_school_group,
    signup_teacher,
    update_user_profile_avatar,
    get_all_teacher_account,
    get_all_teachers,
    remove_teacher_account
})(Teachermanagement);