import React from 'react';
import { useState, useEffect, useCallback } from "react";
function Time({ get_new_access_token, props }) {
    const [time_m, setTime_m] = useState(5);
    const [time_sec, setTime_sec] = useState(0);
    const handleReloadQR = useCallback(function () {
        get_new_access_token();
        setTime_m(5)
        setTime_sec(0)
    })
    useEffect(() => {
        const timerID = setInterval(() => {
            setTime_sec(prevSec => {
                if (prevSec <= 0) {
                    setTime_m(prevMin => {
                        if (prevMin <= 0) {
                            setTime_sec(0)
                            return 0
                        }
                        else if (prevSec === 0 && prevMin === 0) {
                            return 0
                        }
                        return prevMin - 1
                    })
                    return prevSec = 59
                }
                return prevSec - 1;
            });
        }, 1000)
        return () => clearInterval(timerID)
    }, []);
    // console.log(get_new_access_token)
    return (
        <div className="flip--card__created__Datetime">
            <span className="card--time">
                <span>
                    {time_m < 10 && 0}{time_m}:{time_sec < 10 && 0}{time_sec}</span>
                {time_m === 0 && time_sec === 0 ? (<span className="card--time__expdate"></span>) : ("")}
            </span>
            <div className="modal--qr__controller">
                <i className="btn--close__mobile btn--close__pc fa-solid fa-circle-xmark" onClick={(props)}></i>
                <i className="btn--update fa-solid fa-rotate-right" onClick={handleReloadQR}></i>
            </div>
        </div>)
}

export default React.memo(Time);
