import { connect } from "react-redux";
import bg_login_img from '../static/images/bg-01.jpg';
import { change_password } from "../actions/auth";
import { useState, useEffect } from "react";
import { Navigate } from 'react-router-dom';
import Login from "./Login";
const ChangePassword = ({ isAuthenticated, change_password, res_mess, success }) => {
    const [item, setItem] = useState({
        old_password: "",
        new_password: "",
        confirm_password: ""
    });
    const [errors, setErrors] = useState([]);
    const { old_password, new_password, confirm_password } = item;
    const onChange = (e) => {
        if (e.target.value === "") {
            onBlur(e)
        }
        return setItem({ ...item, [e.target.name]: e.target.value })
    }
    const $ = document.querySelector.bind(document);
    const onBlur = (e) => {
        const loginForm = $("#change_password_form")
        const label = loginForm.querySelector(`label[for="${e.target.name}"]`)
        // const fields = loginForm.querySelectorAll('input.input-field')

        // fields.forEach((field) => {
        if (e.target.value == '') {
            if (!(errors.includes(`${label.textContent}`))) {
                setErrors(prev => [...prev, `${label.textContent}`])
            }
        }
        else {
            if ((errors.includes(`${label.textContent}`))) {
                setErrors(prevs => prevs.filter(prev => prev !== `${label.textContent}`))
            }
        }
    }
    useEffect(() => {
        setErrors([res_mess])
    }, [res_mess])
    const onSubmit = e => {
        e.preventDefault();
        if (new_password === confirm_password) {
            change_password(old_password, new_password)
        }
        else {
            setErrors([""])
        }
    }
    if (success) {
        return <Navigate to="/home" />
    }
    return (
        <>
            {
                isAuthenticated ? <div id="change_password_form" className="wrapper--login">
                    <div className="login--container" style={{
                        background: `url(${bg_login_img}) center / cover `
                    }}>
                        <form className="login--form validate-form" method="post" onSubmit={e => onSubmit(e)}>
                            <h1 className="login--form__title">
                                
                            </h1>
                            <span className="wrapper--logger">

                                {errors && errors.map((error, i) => (<span key={i}>{error}</span>))}
                                { }
                            </span>
                            <div className="wrapper--input validate-input" data-validate="Username is reauired">
                                <label className="input--label input--label__username"
                                    htmlFor="old_password"></label>
                                <div className="wrapper--fields wrapper--fields__username">

                                    <i className="login--form__icon fa-solid fa-lock"></i>
                                    <input className="login--input__fields input__username"
                                        type="password"
                                        placeholder="Old Password"
                                        id="old_password"
                                        name="old_password"
                                        value={old_password}
                                        onChange={e => onChange(e)}
                                        onBlur={onBlur}
                                    />

                                    <span className="input--focus"></span>
                                </div>
                            </div>
                            <div className="wrapper--input validate-input" data-validate="Username is reauired">
                                <label className="input--label input--label__username"
                                    htmlFor="new_password"></label>
                                <div className="wrapper--fields wrapper--fields__username">

                                    <i className="login--form__icon fa-solid fa-lock"></i>
                                    <input className="login--input__fields input__username"
                                        type="password"
                                        id="new_password"
                                        name="new_password"
                                        placeholder="New Password"
                                        value={new_password}
                                        onChange={e => onChange(e)}
                                        onBlur={onBlur}
                                    />

                                    <span className="input--focus"></span>
                                </div>
                            </div>

                            <div className="wrapper--input validate-input" data-validate="Password is required">
                                <label className="input--label input--label__password"
                                    htmlFor="confirm_password"></label>
                                <div className="wrapper--fields wrapper--fields__username">
                                    <i className="login--form__icon fa-solid fa-lock"></i>
                                    <input className="login--input__fields input__password"
                                        type="password"
                                        id="confirm_password"
                                        name="confirm_password"
                                        value={confirm_password}
                                        placeholder="Confirm Password"
                                        onChange={e => onChange(e)}
                                        onBlur={onBlur}
                                    />
                                    <span className="input--focus"></span>
                                </div>
                            </div>
                            <div className="wrapper--btn">
                                <div className="btn--container">
                                    <div className="btn--primary__bg"></div>
                                    <button className="btn btn--login" type="submit">
                                        UPDATE PASSWORD
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div > : <Login />
            }
        </>

    )
}

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    res_mess: state.auth.res_mess,
    success: state.auth.success
})
export default connect(mapStateToProps, { change_password })(ChangePassword);