export const checkAuthenticated = () => async dispatch => {
    if (localStorage.getItem('access')) {
        const config = {
            headers: {
                'Content-Type': 'application/json',
            }
        };

        const body = JSON.stringify({ token: localStorage.getItem('access') });

        try {
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/jwt/verify/`, body, config)
            if (res.data.code !== 'token_not_valid') {
                dispatch({
                    type: AUTHENTICATED_SUCCESS
                });
            } else {
                dispatch({
                    type: AUTHENTICATED_FAIL
                });
            }
        } catch (err) {
            dispatch({
                type: AUTHENTICATED_FAIL
            });
        }
    } else {
        dispatch({
            type: AUTHENTICATED_FAIL
        });
    }
};
export const get_new_access_token = () => async dispatch => {
    if (localStorage.getItem('refresh')) {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem('access')}`,
                "Content-type": "application/json"
            }
        }
        const body = JSON.stringify({ refresh: localStorage.getItem('refresh') });
        try {
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/jwt/refresh/`, body, config);
            if (res.data.access) {

                dispatch({
                    type: UPDATE_ACCESS_TOKEN_SUCCESS,
                    payload: res.data
                })
            }
            else {
                dispatch({
                    type: UPDATE_ACCESS_TOKEN_FAIL,
                })
            }
        }
        catch {
            dispatch({
                type: UPDATE_ACCESS_TOKEN_FAIL,
            })
        }
    }
};

export const load_user = () => async dispatch => {
    if (localStorage.getItem("access")) {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "content-type": "application/json"
            }
        };
        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/users/me/`, config);
            dispatch({
                type: USER_LOADED_SUCCESS,
                payload: res.data
            });
        }
        catch (err) {
            dispatch({
                type: USER_LOADED_FAIL
            });
        }
    }
    else {
        dispatch({
            type: USER_LOADED_FAIL
        });
    }
};


export const login = (username, password) => async dispatch => {
    const config = {
        headers: {
            'Content-Type': 'application/json',
        }
    };
    const body = JSON.stringify({ username, password });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/jwt/create/`, body, config);
        console.log(`${process.env.REACT_APP_API_URL}/auth/jwt/create/`);
        dispatch({
            type: LOGIN_SUCCESS,
            payload: res.data
        });
        dispatch(load_user());
        dispatch(load_user_profile());
    }
    catch (err) {
        dispatch({
            type: LOGIN_FAIL,
            payload: ""
        });

    }
};

export const signup = (username, email, password, re_password) => async dispatch => {
    const config = {
        headers: {
            'Content-Type': 'application/json',
        }
    };

    const body = JSON.stringify({ username, email, password, re_password });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/auth/users/`, body, config, { withCredentials: true });

        dispatch({
            type: SIGNUP_SUCCESS,
            payload: res.data
        });
    }
    catch (err) {
        dispatch({
            type: SIGNUP_FAIL
        });
    }
};

