export const load_user_profile = () => async dispatch => {
    if (localStorage.getItem("access")) {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "Content-Type": "application/json"
            }
        };

        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/profile/user`, config);
            if (res.data.error) {
                dispatch({
                    type: LOAD_USER_PROFILE_FAIL
                });
            } else {
                dispatch({
                    type: LOAD_USER_PROFILE_SUCCESS,
                    payload: res.data
                });
            }
        } catch (error) {
            dispatch({
                type: LOAD_USER_PROFILE_FAIL
            });
        }
    }
};

export const update_user_profile = (fullname, all_group_name, address, phone) => async dispatch => {
    const config = {
        headers: {
            "Authorization": `JWT ${localStorage.getItem("access")}`,
            "Content-Type": "application/json",
        }
    };

    const body = JSON.stringify({
        fullname, all_group_name, address, phone
    });

    try {
        const res = await axios.put(`${process.env.REACT_APP_API_URL}/profile/user_update`, body, config);
        if (res.data) {
            dispatch({
                type: UPDATE_USER_PROFILE_SUCCESS,
                payload: res.data
            });
            dispatch(load_user_profile());
        } else {
            dispatch({
                type: UPDATE_USER_PROFILE_FAIL
            });
        }
    } catch (err) {
        dispatch({
            type: UPDATE_USER_PROFILE_FAIL
        });
    }
};

