export const change_password = (old_password, new_password) => async dispatch => {
    const config = {
        headers: {
            "Authorization": `JWT ${localStorage.getItem("access")}`,
            'Content-Type': 'application/json'
        }
    }
    const body = JSON.stringify({ old_password, new_password });
    try {
        const res = await axios.put(`${process.env.REACT_APP_API_URL}/change_password`, body, config);
        if (res.data.error) {
            dispatch({
                type: CHANGE_PASSWORD_FAIL,
                payload: res.data.error
            })
        }

        else {

            dispatch({
                type: CHANGE_PASSWORD_SUCCESS,
                payload: res.data
            });
            dispatch(checkAuthenticated());
        }
    } catch {
        dispatch({
            type: CHANGE_PASSWORD_FAIL
        })
    }
}

