export const create_post = (title, content) => async dispatch => {
    const config = {
        headers: {
            "Authorization": `JWT ${localStorage.getItem("access")}`,
            'Content-Type': 'application/json'
        }
    };
    const body = JSON.stringify({
        title,
        content
    })

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/portal/create_post`, body, config)
        if (res.data.success) {

            dispatch({ type: CREATE_POST_SUCCESS });
            dispatch(get_post(3));
            dispatch(get_userinfor_post(3));
        }
        else {
            dispatch({ type: CREATE_POST_FAIL })
        }
    }
    catch {
        dispatch({ type: CREATE_POST_FAIL })
    }
}

export const update_post = (title, content, pk) => async dispatch => {
    const config = {
        headers: {
            "Authorization": `JWT ${localStorage.getItem("access")}`,
            "content-type": "application/json"
        }
    }
    const body = JSON.stringify({ title, content })
    try {
        const res = await axios.put(`${process.env.REACT_APP_API_URL}/portal/update_post/${pk}`, body, config);
        if (res.data) {
            dispatch({
                type: UPDATE_POST_SUCCESS,
                payload: res.data
            });
        }
        else {
            dispatch({ type: UPDATE_POST_FAIL })
        }
    }
    catch {
        dispatch({ type: UPDATE_POST_FAIL })
    }

}

