import React, { useState, useEffect, useLayoutEffect } from "react";
// import { Link } from 'react-router-dom'
import { connect } from 'react-redux';
import { get_post, get_userinfor_post, create_post, update_post, remove_post, load_user } from "../actions/auth";
// import Login from "../containers/Login";
import ReactReadMoreReadLess from "react-read-more-read-less";
import moment from "moment";
import { render } from "react-dom";
// import auth from "../reducers/auth";

const Home = ({ isAuthenticated, get_post, get_userinfor_post,
    posts, posters, user, create_post, update_post, load_user, visible,
    isLoad, remove_post }) => {
    const [mounted, setMounted] = useState(false);
    const [edit, setEdit] = useState(false);
    const [remove, setRemove] = useState(false);
    const [add, setAdd] = useState(false);
    const [send, setSend] = useState(false);
    const [loading, setLoading] = useState(false);
    const [item, setItem] = useState({
        title: "",
        content: ""
    });
    const { title, content } = item;
    const [news, setNews] = useState([]);
    const $ = document.querySelector.bind(document);

    const [itemNum, setItemNum] = useState(3);

    const handleRemove = (item) => {
        setEdit(false);
        handleOpenModal();
        setAdd(false);
        setRemove(true);
        setItem(item);
    };

    function handleCloseModal() {
        const group = $(".group-modal");
        group.classList.remove("open");
        setTimeout(() => {
            setMounted(!mounted)
            setSend(false)
            setAdd(false);
            setEdit(false)
            setRemove(false);
        }, 200)
    };
    const handleUpdatePost = (item) => {
        setEdit(true)
        setAdd(false)
        handleOpenModal()
        setItem(item)
    };
    // const   
    const handleAddPost = () => {
        setEdit(false)
        setAdd(true)
        handleOpenModal();
    };

    function handleOpenModal() {
        const group = $(".group-modal");
        group.classList.add("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200);
        setRemove(false)
        setItem({
            title: "",
            content: ""
        });
    }

    const onChange = e => {
        setItem({ ...item, [e.target.name]: e.target.value });
    }

    const onSubmit = e => {
        e.preventDefault();
        if (remove) {
            remove_post(item.id);
            setSend(true);
            setItemNum(3);
        }
        if (add) {
            create_post(title, content);
            setSend(true);
            setItemNum(3);
        }
        if (edit) {
            update_post(title, content, item.id);
            setSend(true);
            setItemNum(3);
        }
    };

    if (send) {
        setTimeout(() => {
            handleCloseModal();
        }, 200);
        setSend(false)
    };

    const handleOptions = (e) => {
        // e.preventDefault();
        if ($("i ul.open")) {
            $("i ul.open").classList.remove("open")
        }
        if (e.target.querySelector("i ul")) {
            e.target.querySelector("i ul").classList.add("open")
        }
        else {
            $("i ul").classList.remove("open")
        }
        // e.target
        // e.target.querySelector("i ul").classList.add("open");
    };

    const handleLoadMore = () => {
        setLoading(true)
        setTimeout(() => {
            setItemNum(prev => prev + 3);
            setLoading(false)
        }, 300)
    };

    useEffect(() => {
        get_userinfor_post(itemNum);
        get_post(itemNum);
    }, [itemNum]);

    useEffect(() => {
        setNews(prev => [...prev, ...posts]);
        if (edit) {
            setNews(prev => [...posts])
        }
        if (remove) {
            setNews(prev => [...posts])
        }
        if (add) {
            setNews(prev => [...posts])
        }
    }, [posts]);
    useEffect(() => {
        load_user();
        return (setNews(prev => { return [] }));
    }, []);
    return (
        <div className="wrapper--container">
            {
                news.map((item, index) => (
                    <div className="post--container" key={index} onClick={e => handleOptions(e)}>
                        <div className="post--header" >
                            <div className="post--header__img">
                                {posters.map((poster, i) => {
                                    if (poster.user_id === item.poster) {
                                        return (
                                            <img key={i} className="post--user__avatar"
                                                src={poster.avatar_set} alt="" width="150px" />
                                        )
                                    }
                                })}
                            </div>
                            <div className="post--header__info">
                                {posters.map((poster, i) => {
                                    if (poster.user_id === item.poster) {
                                        return (
                                            <span key={i} className="post--header__username">{poster.fullname_set}</span>
                                        )
                                    }
                                })}
                                <span className="post--header__datetime">{moment(item.created).format("YYYYMMDD")}</span>
                                <span className="post--header__datetime"> {moment(item.created).startOf('min').fromNow()}</span>
                            </div>
                            <div className="post--header__pin" ><i className="fa-solid fa-thumbtack"></i></div>
                            {user && user.id === item.poster ? (
                                <div className="post--header__options options--open"
                                >
                                    <i className="fa-solid fa-ellipsis-vertical" >
                                        <ul >
                                            <li onClick={() => handleRemove(item)}></li>
                                            <li onClick={() => handleUpdatePost(item)}></li>
                                        </ul>
                                    </i>
                                </div>
                            ) : ("")
                            }
                        </div >

                        <h1 className="post--header__title">{item.title}</h1>
                        <div className="post--contain" >
                            <ReactReadMoreReadLess charLimit={200}
                                readMoreText={""}
                                readLessText={""}
                                readMoreClassName="read-more-less--more"
                                readLessClassName="read-more-less--less">{item.content}</ReactReadMoreReadLess>
                        </div>

                    </div >
                ))
            }
            <div className="btn post--btn" onClick={handleAddPost}><i className="fa-solid fa-pen-to-square"></i></div>
            <div className={`group-modal ${mounted ? 'open' : ""}`}>
                <div className="modal--contain">


                    <div className="modal--contain__header">
                        {remove ? <h1 style={{ padding: "20px 10px", display: "flex", justifyContent: "center" }}></h1> :
                            <h1>{edit ? "" : ""}</h1>
                        }
                        <i className="group--icon group--remove fa-solid fa-xmark" onClick={handleCloseModal}></i>
                    </div>

                    <form method="post" className="group-form" onSubmit={e => onSubmit(e)}>
                        {remove ? (
                            <>
                            </>
                        )
                            :
                            (
                                <>
                                    <div className="wrapper--input">
                                        <label className="input--label"
                                            htmlFor="title">
                                        </label>
                                        <input className="input__fields"
                                            type='text'
                                            id="title"
                                            name="title"
                                            placeholder=""
                                            value={title}
                                            onChange={e => onChange(e)}
                                            maxLength={255}
                                            required
                                        />
                                    </div>
                                    <div className="wrapper--input">
                                        <label className="input--label"
                                            htmlFor="content"></label>
                                        <textarea className="input__fields"
                                            type="button"
                                            id="content"
                                            name="content"
                                            placeholder=""
                                            value={content}
                                            onChange={e => onChange(e)}
                                            required
                                        />
                                    </div>
                                </>
                            )}

                        <button className="btn group--btn primary" type="submit">{remove ? "" : edit ? "" : ""}</button>
                    </form>
                </div>
                <div className="modal--group__bg" onClick={handleCloseModal}></div>
            </div >
            {
                loading ? (<div id="layer1" className="layer1_class">
                    <svg className="spinner" width="60" height="60">
                        <circle className="path" cx="30" cy="30" r="20" stroke="red" strokeWidth="5" fill="none" />
                    </svg>
                </div>) : (isLoad ? (
                    <div className="load-item-more" onClick={handleLoadMore}></div>) :
                    <p className="load-item-FAIL"></p>)
            }


        </div >
    )

}
const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    posts: state.auth.posts,
    posters: state.auth.posters,
    user: state.auth.user,
    visible: state.auth.visible,
    isLoad: state.auth.isLoad,
})

export default connect(mapStateToProps, {
    get_post, get_userinfor_post, create_post,
    update_post, load_user,
    remove_post
})(Home);