import React, { useState, useEffect } from "react";
import { connect } from 'react-redux';
import { load_class_school, create_class_school, update_class_school, remove_class_group } from '../actions/auth';

function ClassManagement({ load_class_school, classes_school, create_class_school, res_mess, update_class_school, remove_class_group, success }) {
    const $ = document.querySelector.bind(document);
    const [mounted, setMounted] = useState(false);
    const [edit, setEdit] = useState(false);
    const [send, setSend] = useState(false);

    function handleCloseModal() {
        const group = $(".group-modal");
        group.classList.remove("open");
        setTimeout(() => {
            setMounted(!mounted)
            setSend(false)
        }, 200)
    }
    function handleOpenModal() {
        const group = $(".group-modal");
        group.classList.add("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200);
        setItem({
            class_name: "",
            class_course: "",
            class_manager: "",
            class_studentnumber: 0,
            class_submanager: ""
        });
        setEdit(false);
    }

    useEffect(() => {
        load_class_school()
    }, [])

    const [item, setItem] = useState({
        class_name: "",
        class_course: "",
        class_manager: "",
        class_studentnumber: 0,
        class_submanager: ""
    })
    const { class_name,
        class_course,
        class_manager,
        class_submanager,
        class_studentnumber } = item;

    const handleItemOpenItem = (group) => {
        handleOpenModal()
        setEdit(true)
        setItem(group)
    }
    const onChange = e => {
        setItem({ ...item, [e.target.name]: e.target.value });
    }

    const handleRemove = () => {
        remove_class_group(item.id)
        setSend(true)
    }
    const onSubmit = (e) => {
        e.preventDefault()

        if (!edit) {
            create_class_school(class_name, class_course, class_manager, class_submanager, class_studentnumber)
            // setSend(true)
        }
        else {
            update_class_school(class_name, class_course, class_manager, class_submanager, class_studentnumber, item.school_group, item.id);
            if (success) {
                setSend(true)
            }
        }

    };

    if (send) {
        handleCloseModal()
        setSend(false)
    }
    return (
        <div className="wrapper--group__container">

            <div className="header--groupmanagement">
                <h1 className="container--title"></h1>
                <div className="groupmanagement__controller">
                    <i className="group--icon group--add fa-solid fa-plus" onClick={handleOpenModal}></i>
                </div>
            </div>

            <ul className="group--list">
                {classes_school.map((class_school, index) => {
                    return (
                        <li key={index} className="group--items" onClick={() => handleItemOpenItem(class_school)}>
                            <h3>{class_school.class_name}</h3>
                            <div className="group--description">
                                <span><b>{class_school.class_course}</b></span>
                                <span><b>{class_school.class_manager}</b></span>
                                <span><b>{class_school.class_submanager}</b></span>
                                <span><b>{class_school.class_studentnumber}</b></span>
                            </div>
                        </li>
                    )
                })}
            </ul>
            <div className={`group-modal ${mounted ? 'open' : ""}`}>
                <div className="modal--contain">
                    <div className="modal--contain__header">
                        <h1>{edit ? "" : ""}</h1>
                        <i className="group--icon group--remove fa-solid fa-xmark" onClick={handleCloseModal}></i>
                    </div>
                    {edit ? (<button className="group--btn__remove" onClick={handleRemove}></button>) : ""}
                    <form method="post" className="group-form" onSubmit={e => onSubmit(e)}>
                        <p style={{ color: "red", padding: "5px 0 12px" }}>{res_mess && res_mess}</p>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="class_name">
                            </label>
                            <input className="input__fields"
                                type='text'
                                id="class_name"
                                name="class_name"
                                placeholder=""
                                value={class_name}
                                onChange={e => onChange(e)}
                                maxLength={255}
                                required
                            />

                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="class_course">:</label>
                            <input className="input__fields"
                                type='text'
                                id="class_course"
                                placeholder=""
                                name="class_course"
                                value={class_course}
                                onChange={e => onChange(e)}
                                maxLength={255}
                                required
                            />
                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="class_manager">:</label>
                            <input className="input__fields"
                                type='text'
                                id="class_manager"
                                name="class_manager"
                                placeholder=""
                                value={class_manager}
                                maxLength={255}
                                onChange={e => onChange(e)}
                                required
                            />
                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="class_submanager"></label>
                            <input className="input__fields"
                                type='text'
                                id="class_submanager"
                                name="class_submanager"
                                placeholder=""
                                value={class_submanager}
                                onChange={e => onChange(e)}
                                maxLength={20}
                                required
                            />
                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="class_studentnumber"></label>
                            <input className="input__fields"
                                type='number'
                                id="class_studentnumber"
                                name="class_studentnumber"
                                placeholder=""
                                value={class_studentnumber}

                                onChange={e => onChange(e)}
                                required
                            />
                        </div>
                        <button className="btn group--btn primary" type="submit">{edit ? "" : ""}</button>
                    </form>
                </div>
                <div className="modal--group__bg" onClick={handleCloseModal}></div>
            </div >
        </div >
    )
}
const mapStateToProps = state => ({
    // school_groups: state.auth.school_groups,
    // school_group: state.auth.school_group
    classes_school: state.auth.classes_school,
    res_mess: state.auth.res_mess,
    success: state.auth.success
})

export default connect(mapStateToProps, { load_class_school, create_class_school, update_class_school, remove_class_group })(ClassManagement);