import React, { useState } from "react";
import { Navigate, useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import { verify } from '../actions/auth';
import bg_login_img from '../static/images/bg-01.jpg';
const Activate = ({ verify, props }) => {
    const [verified, setVerified] = useState(false);
    const { uid: uid } = useParams()
    const { token: token } = useParams()
    const verify_account = () => {
        verify(uid, token);
        setVerified(true);
    };
    // is the user authenticated?
    // change redirect them to the home page
    if (verified) {
        return <Navigate to='/' />
    }


    return (
        <div id="verify" className="wrapper--verify">
            <div className="verify--container" style={{ background: `url(${bg_login_img}) center / cover ` }}>
                <div className="verify--form validate-form" >
                    <h1 className="verify--form__title">
                        
                    </h1>
                    <span class="verify--description">
                        
                    </span>
                    <button className="btn verify btn--login btn--primary__bg"
                        onClick={verify_account}
                        type="button" >
                        
                    </button>
                </div>
            </div>
        </div>
    );
}


export default connect(null, { verify })(Activate);