import Pusher from "pusher-js";
import React from 'react'
import { useState, useEffect, useRef } from "react";
import { connect } from 'react-redux';
import { load_user, send_messsage } from '../actions/auth';
const Chat = ({ load_user, user, send_messsage }) => {
    const [message, setMessage] = useState("");
    const [messages, setMessages] = useState([]);
    const [counter, setCounter] = useState(1);
    let allMessages = [];

    let ref = useRef();
    ref.current = setInterval(() => {
        setCounter(prev => prev + 1)
    }, 1000 * 3);
    useEffect(() => {
        Pusher.logToConsole = true;

        var pusher = new Pusher('801d63bcd07dc6085acf', {
            cluster: 'ap3'
        });

        const channel = pusher.subscribe('chat');
        channel.bind('message', function (data) {
            allMessages.push(data);
            setMessages(allMessages);
            setMessage("")
        });
    }, []);


    useEffect(() => {
        load_user();
        return clearInterval(ref.current)
    }, [])
    const onSubmit = (e) => {
        e.preventDefault();
        send_messsage(message);
    }
    const onChange = (e) => {
        setMessage(e.target.value)
    }

    return (
        <div className="wrapper--container messege">
            <ul className="messege-list">
                {
                    messages.map((m, i) => (
                        <>
                            <li className="messege-item" key={i}>
                                <h4 className="chat--username">{m.username}</h4>
                                <p className="chat--contents">{m.message}</p>
                            </li>
                            <hr />
                        </>
                    ))
                }
            </ul>

            <form className="form-messege" method="POST"
                onSubmit={e => onSubmit(e)}
            >
                <input class="input--wrapper__messege" value={message}
                    onChange={(e) => onChange(e)} />
                <button type="submit"></button>
            </form>
        </div>
    )
}

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    user: state.auth.user
})
export default connect(mapStateToProps, { send_messsage, load_user })(Chat);