import React, { useState } from "react";
import { Link, Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { login } from '../actions/auth';
import bg_login_img from '../static/images/bg-01.jpg';
const Login = ({ login, isAuthenticated, res_mess }) => {
    const [formData, setFormData] = useState({
        username: '',
        password: ''
    });

    const { username, password } = formData;
    const [errors, setErrors] = useState([])
    const $ = document.querySelector.bind(document);

    const onChange = e => {
        if (e.target.value === "") {
            onBlur(e)
        }
        return setFormData({ ...formData, [e.target.name]: e.target.value });
    }


    const onSubmit = e => {
        e.preventDefault();
        login(username, password);
    };

    const onBlur = (e) => {
        const loginForm = $("#login-form")
        const label = loginForm.querySelector(`label[for="${e.target.name}"]`)
        // const fields = loginForm.querySelectorAll('input.input-field')

        // fields.forEach((field) => {
        if (e.target.value === '') {
            if (!(errors.includes(`${label.textContent}`))) {
                setErrors(prev => [...prev, `${label.textContent}`])
            }
        }
        else {
            if ((errors.includes(`${label.textContent}`))) {
                setErrors(prevs => prevs.filter(prev => prev !== `${label.textContent}`))
            }
        }

    }

    // Validators()

    // is the user authenticated?
    // change redirect them to the home page

    if (isAuthenticated) {
        return <Navigate to="/home" />
    }
    return (
        <div id="login" className="wrapper--login" >
            <div className="login--container" style={{ background: `url(${bg_login_img}) center / cover ` }}>
                <div className="login--form validate-form" >
                    <h1 className="login--form__title">
                        
                    </h1>

                    <span className="wrapper--logger">
                        {errors && errors.map((error, i) => (<span key={i}>{error}</span>))}
                        {res_mess && res_mess}
                    </span>
                    <form method="post" style={{ width: '100%' }} id="login-form" onSubmit={e => onSubmit(e)}>
                        <div className="wrapper--input validate-input">
                            <label className="input--label input--label__username"
                                htmlFor="username"></label>
                            <div className="wrapper--fields wrapper--fields__username">
                                <i className="login--form__icon fa-regular fa-user"></i>
                                <input className="login--input__fields input-field input__username"
                                    id="username"
                                    type='username'
                                    name="username"
                                    value={username}
                                    onChange={e => onChange(e)}
                                    onBlur={e => onBlur(e)}
                                    placeholder="taroukun"
                                    required
                                />
                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className=" wrapper--input validate-input">
                            <label className="input--label input--label__password" htmlFor="password"></label>
                            <div className="wrapper--fields wrapper--fields__username">
                                <i className="login--form__icon fa-solid fa-lock"></i>
                                <input className="login--input__fields input-field  input__password"
                                    id="password"
                                    type='password'
                                    name="password"
                                    value={password}
                                    onChange={e => onChange(e)}
                                    onBlur={e => onBlur(e)}
                                    required
                                    placeholder="" />
                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className=" login--form__policy">
                            <a className="login--form__link"></a>
                        </div>


                        <div className="wrapper--btn">
                            <div className="btn--container">
                                <div className="login--signup__link-wrapper">
                                    <Link className="login--signup__link" to="/signup"></Link>
                                    <Link className="login--signup__link" to="/reset-password"></Link>
                                </div>
                                <button className="btn btn--login btn--primary__bg" type="submit">
                                    
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    res_mess: state.auth.res_mess
})

export default connect(mapStateToProps, { login })(Login);