import React, { useState, useEffect, useCallback } from "react";
import { connect } from 'react-redux';
import { load_user_profile, update_user_profile, load_user_profile_avatar, update_user_profile_avatar } from '../actions/auth';
import default_user from '../static/images/default_user.jpeg';

const Introduction = ({ isAuthenticated, load_user_profile, update_user_profile, load_user_profile_avatar, user_information, school_avt, update_user_profile_avatar }) => {

    const $ = document.querySelector.bind(document)

    const [mounted, setMounted] = useState(false);
    const [editUserProfile, setEditUserProfile] = useState(false);
    const [editUserAvatar, setEditUserAvatar] = useState(false);
    const [send, setSend] = useState();
    const [demoavatar, setDemoAvatar] = useState(false);
    const [avatar, setAvatar] = useState(school_avt);
    const [item, setItem] = useState(
        user_information
    );
    const { fullname, all_group_name, address, phone } = item;


    const handleChangeAvatar = (e) => {
        const file = e.target.files[0]
        file.preview = URL.createObjectURL(file)
        setAvatar(file)
        setDemoAvatar(file);
    }

    useEffect(() => {
        return () => {
            demoavatar && URL.revokeObjectURL(demoavatar.preview)
        }
    }, [demoavatar]);

    useEffect(() => {
        load_user_profile_avatar();
        load_user_profile();
    }, [])
    const handleOpenModal = useCallback(() => {
        const group = $(".group-modal");
        group.classList.add("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200);
    }, [])

    const handleAvatarModal = () => {
        handleOpenModal()
        setEditUserAvatar(true)
        setItem(user_information)
    }
    const handleProfileModal = () => {
        handleOpenModal()
        setEditUserProfile(true)
        setItem(user_information)
    }

    const handleCloseModal = useCallback(() => {
        const group = $(".group-modal");
        group.classList.remove("open");
        setTimeout(() => {
            setMounted(!mounted)
            setEditUserProfile(false);
            setEditUserAvatar(false);
        }, 200);
    }, [])
    const onChange = useCallback((e) => {
        setItem({ ...item, [e.target.name]: e.target.value })
    })
    const onSubmit = e => {
        e.preventDefault();
        if (editUserProfile) {
            update_user_profile(fullname, all_group_name, address, phone);
        }
        if (editUserAvatar) {
            update_user_profile_avatar(avatar, item.user);
        }
        setSend(!send);
        handleCloseModal();
    }
    useEffect(() => {
    }, [school_avt]);
    return (
        isAuthenticated ? (
            <div className="group-introduction">
                <div className="group-introduction__edit">
                    <button
                        className="introduction--btn introduction--btn__edit"
                        onClick={handleProfileModal}
                    ></button>
                </div>

                <ul className="group-introduction__list">
                    {
                        school_avt == null ?
                            (
                                <li className="group-introduction__item img" onClick={handleAvatarModal}>
                                    <img className="default_user"
                                        title="default img" alt="default img"
                                        onError={(e) => { e.target.src = default_user }}
                                        value
                                        src={""}
                                    />
                                    <div onClick={handleAvatarModal}><i className="fa-solid fa-pen-to-square"></i></div>
                                </li>
                            ) : (
                                <li className="group-introduction__item img" title="user image" onClick={handleAvatarModal}>
                                    <img className="default_user" src={school_avt} alt="profile img" onError={(e) => { e.target.src = default_user }} />
                                    <div onClick={handleAvatarModal}><i className="fa-solid fa-pen-to-square"></i></div>
                                </li>)}
                    <li className="group-introduction__item">
                        <span><span></span></span>
                        <p>
                            {user_information.address}
                        </p>
                    </li>
                    {/* <li className="group-introduction__item">
                        <span><span></span></span>
                        <p>
                            {user_information.date_of_birth}
                        </p>
                    </li> */}
                    <li className="group-introduction__item">
                        <span><span></span></span>
                        <p>
                            {user_information.fullname}
                        </p>
                    </li>
                    <li className="group-introduction__item">
                        <span><span></span></span>
                        <p>
                            {user_information.all_group_name}
                        </p>
                    </li>
                    <li className="group-introduction__item">
                        <span><span></span></span>
                        <p>
                            {user_information.phone}
                        </p>
                    </li>
                </ul>
                <div className={`group-modal ${mounted ? "open" : ""}`}>
                    <div className="modal--contain">
                        <div className="modal--contain__header">
                            <h1></h1>
                            <i className="group--icon group--remove fa-solid fa-xmark" onClick={handleCloseModal}></i>
                        </div>

                        <form method="post" className="group-form" onSubmit={e => onSubmit(e)}>
                            {editUserAvatar && <div className="wrapper--input">

                                <div style={{ height: "110px", }}>
                                    {
                                        <img src={demoavatar && demoavatar.preview || default_user}
                                            style={{ width: "80px", position: "absolute" }}
                                            onError={(e) => { e.target.src = default_user }}
                                        />
                                    }
                                </div>
                                <label className="avt-input">
                                    <input type="file"
                                        onChange={handleChangeAvatar}
                                    />
                                    
                                </label>
                            </div>}
                            {editUserProfile &&
                                <>
                                    <div className="wrapper--input">
                                        <label className="input--label"
                                            htmlFor="fullname">
                                        </label>
                                        <input className="input__fields"
                                            type='text'
                                            id="fullname"
                                            name="fullname"
                                            placeholder=""
                                            value={fullname}
                                            onChange={e => onChange(e)}
                                            required
                                        />
                                    </div>
                                    <div className="wrapper--input">
                                        <label className="input--label"
                                            htmlFor="all_group_name">
                                        </label>
                                        <input className="input__fields"
                                            type='text'
                                            id="all_group_name"
                                            name="all_group_name"
                                            placeholder=""
                                            value={all_group_name}
                                            onChange={e => onChange(e)}
                                            required
                                        />
                                    </div>
                                    <div className="wrapper--input">
                                        <label className="input--label"
                                            htmlFor="phone">
                                        </label>
                                        <input className="input__fields"
                                            type='tel'
                                            id="phone"
                                            name="phone"
                                            placeholder=""
                                            value={phone}
                                            onChange={e => onChange(e)}
                                            required
                                        />
                                    </div>
                                    <div className="wrapper--input">
                                        <label className="input--label"
                                            htmlFor="address">
                                        </label>
                                        <input className="input__fields"
                                            type='text'
                                            id="address"
                                            name="address"
                                            placeholder=""
                                            value={address}
                                            onChange={e => onChange(e)}
                                            required
                                        />
                                    </div>
                                </>
                            }

                            <button className="btn group--btn primary" type="submit"></button>
                        </form>

                    </div>
                    <div className="modal--group__bg" onClick={handleCloseModal}></div>
                </div >
            </div>
        ) : ("login")
    )
}

const mapStateToProps = state => ({
    school_avt: state.auth.avatar,
    user_information: state.auth.user_information,
    isAuthenticated: state.auth.isAuthenticated
})


export default connect(mapStateToProps, { load_user_profile, update_user_profile, load_user_profile_avatar, update_user_profile_avatar })(Introduction);