import comingsoon from '../static/images/comingsoon.gif';
const Help = () => {
    return (
        <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "calc(100vh - 200px)", overflow: "hidden" }}>
            <img src={comingsoon} style={{ height: "100%", backgroundRepeat: "no-repeat", objectFit: "cover" }} alt="comingsoon" />
        </div>
    )
}
export default Help