import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';

// component
import Home from './containers/Home';
import Login from './containers/Login';
import Signup from './containers/Signup';
import Activate from './containers/Activate';
import ResetPassword from './containers/ResetPassword';
import ResetPasswordConfirm from './containers/ResetPasswordConfirm';
import Groupmanagement from './containers/Groupmanagement';
import Introduction from './containers/Introduction';
import Card from './containers/Card';
import Teachermanagement from './containers/Teachermanagement';
import ChangePassword from "./containers/ChangePassword";
import StudentManagement from './containers/StudentManagement';
import ClassManagement from './containers/ClassManagement';
import Chat from './containers/Chat';
import Help from './containers/Help';

// redux provider
import { Provider } from 'react-redux';
import store from './store';
import Layout from './hocs/Layout';

function App() {
  return (
    <Provider store={store}>
      <Router>
        <Layout>
          <Routes>
            <Route exact path='/home' element={<Home />}></Route>
            <Route path='/login' element={<Login />}></Route>
            <Route path='/signup' element={<Signup />}></Route>
            <Route path='card' element={<Card />}></Route>
            <Route path='/reset-password' element={<ResetPassword />}></Route>
            <Route path='/password/reset/confirm/:uid/:token' element={<ResetPasswordConfirm />}></Route>
            <Route path='/activate/:uid/:token' element={<Activate />}></Route>
            <Route path='/groupmanagement' element={<Groupmanagement />}></Route>
            <Route path='/introduction' element={<Introduction />}></Route>
            <Route path='/teachermanagement' element={<Teachermanagement />}></Route>
            <Route path='/passwordchange' element={<ChangePassword />}></Route>
            <Route path='/studentsmanagement' element={<StudentManagement />}></Route>
            <Route path='/classmanagement' element={<ClassManagement />}></Route>
            <Route path='/chat' element={<Chat />}></Route>
            <Route path="help" element={<Help />}></Route>
          </Routes>
        </Layout>
      </Router>
    </Provider>
  );
}

export default App;