import React, { useState } from "react";
import { Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password } from '../actions/auth';
import bg_login_img from '../static/images/bg-01.jpg';
const ResetPassword = ({ reset_password }) => {
    const [requestSent, setRequestSent] = useState(false);
    const [formData, setFormData] = useState({
        email: '',
    });

    const { email } = formData;
    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });
    const onSubmit = e => {
        e.preventDefault();
        reset_password(email);
        setRequestSent(true);
    };
    // is the user authenticated?
    // change redirect them to the home page

    if (requestSent) {
        return <Navigate to="/" />
    }
    return (

        <div id="signup" className="wrapper--signup">
            <div className="signup--container" style={{ background: `url(${bg_login_img}) center / cover ` }}>
                <div className="sign--form validate-form" method="post">
                    <h1 className="sign--form__title">
                        
                    </h1>
                    <div className="wrapper--logger">
                        <span></span>
                    </div>
                    <form method="post" style={{ width: '100%' }} onSubmit={e => onSubmit(e)}>
                        <div className="wrapper--input validate-input">
                            <label className="input--label input--label__email" htmlFor="email"></label>
                            <div className="wrapper--fields wrapper--fields__email">
                                <i className="signup--form__icon fa-regular fa-envelope"></i>
                                <input className="signup--input__fields input__password"
                                    type="email"
                                    id="email"
                                    name="email"
                                    placeholder=""
                                    value={email}
                                    onChange={e => onChange(e)}
                                    required
                                />

                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className="wrapper--btn">
                            <div className="btn--container resetpassword">
                                <button className="btn btn--login btn--primary__bg" type="submit">
                                    
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default connect(null, { reset_password })(ResetPassword);