import React, { useState } from "react";
import { Navigate, useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password_confirm } from '../actions/auth';
import bg_login_img from '../static/images/bg-01.jpg';
// const useCurrentPath = () => {
//     const location = useLocation()

//     const routes = [{ path: '/password/reset/confirm/:uid/:token' }]
//     const [{ route }] = matchRoutes(routes, location)
//     return route.path
// }
const ResetPasswordConfirm = ({ reset_password_confirm }) => {
    const [requestSent, setRequestSent] = useState(false);
    const [formData, setFormData] = useState({
        new_password: '',
        re_new_password: ''
    });

    const { new_password, re_new_password } = formData;
    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const { uid, token } = useParams()
    const onSubmit = e => {
        // const uid = match.params.uid;
        // const token = match.params.token;
        e.preventDefault();
        alert("s")
        console.log(uid, token)
        reset_password_confirm(uid, token, new_password, re_new_password);
        setRequestSent(true);
    };
    // is the user authenticated?
    // change redirect them to the home page

    if (requestSent) {
        return <Navigate to="/" />
    }
    return (
        <div className="wrapper--signup">
            <div className="signup--container" style={{ background: `url(${bg_login_img}) center / cover ` }}>
                <div className="sign--form validate-form">
                    <h1 className="sign--form__title">
                        
                    </h1>
                    <div className="wrapper--logger">
                        <span></span>
                    </div>
                    <form method="post" style={{ width: '100%' }}
                        onSubmit={e => onSubmit(e)}
                    >
                        <div className=" wrapper--input validate-input">
                            <label className="input--label input--label__password" htmlFor="password"></label>
                            <div className="wrapper--fields wrapper--fields__username">
                                <i className="signup--form__icon fa-solid fa-lock"></i>
                                <input className="signup--input__fields input__password"
                                    type='password'
                                    placeholder='New Password'
                                    name="new_password"
                                    value={new_password}
                                    onChange={e => onChange(e)}
                                    minLength='6'
                                    required
                                />

                                <span className="input--focus"></span>
                            </div>
                        </div>
                        <div className=" wrapper--input validate-input">
                            <label className="input--label input--label__password" htmlFor="confirmpassword"></label>
                            <div className="wrapper--fields wrapper--fields__username">
                                <i className="signup--form__icon fa-solid fa-lock"></i>
                                <input className="signup--input__fields input__password"
                                    type='password'
                                    placeholder='Confirm New Password'
                                    name="re_new_password"
                                    value={re_new_password}
                                    onChange={e => onChange(e)}
                                    minLength='6'
                                    required
                                />
                                <span className="input--focus"></span>
                            </div>
                        </div>
                    </form>

                    <div className="wrapper--btn">
                        <button className="btn--resetpassword btn--resetpassword__primary__bg" type="submit">
                            
                        </button>

                    </div>
                </div>
            </div>
        </div>
    );
};

export default connect(null, { reset_password_confirm })(ResetPasswordConfirm);