import React from 'react'
import { useState, useEffect } from "react";
import { connect } from 'react-redux';
import Time from "./Time";
import Login from "./Login";

import moment from "moment";
import { QRCodeSVG } from 'qrcode.react';
import defaultUser from '../static/images/default_user.jpeg'
import { load_user_profile, load_user, load_user_profile_avatar, get_new_access_token, get_admin_profile } from "../actions/auth";

function Card({ isAuthenticated,
    user_information,
    avatar,
    user, load_user,
    load_user_profile_avatar,
    access, get_new_access_token,
    get_admin_profile,
    admin_profile }) {
    const $ = document.querySelector.bind(document);
    const [mounted, setMounted] = useState(false);
    useEffect(() => {
        load_user_profile();
        load_user();
        load_user_profile_avatar();
        get_admin_profile()
    }, [])
    const { email, username, created_at } = user;
    function handleClickRotate() {
        const cardFlip = $(".flip--card__inner");
        cardFlip.classList.toggle("rotate");
    }
    function handleCloseQR() {
        const QRCard = $(".modal");
        QRCard.classList.remove("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200)
    }
    function handleOpenQR() {
        const QRCard = $(".modal");
        QRCard.classList.add("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200)
    }


    const created = moment(created_at).format("YYYYMMDD");
    const { user_type,
        fullname,
        phone,
        date_of_birth,
        address,
        teacher_belong_to_id,
        teacher_belong_to_name,
        teacher_course,
        teacher_library,
        student_department_name,
        student_class_name,
        student_field_name
    } = user_information;
    return (
        isAuthenticated ?
            (
                <div className="wrapper--container" id="container">
                    <div className="card--qr">
                        <i className="fa-solid fa-qrcode" onClick={
                            handleOpenQR
                        }></i>
                    </div>
                    <div className={`modal ${mounted ? 'open' : ""}`}>
                        <div className="modal--contain">
                            {
                                mounted && <div className="modal--qr">
                                    {/* <img className="modal--qr__img" src={ } alt="qr" /> */}
                                    <QRCodeSVG value={access} />
                                    <Time get_new_access_token={get_new_access_token} props={handleCloseQR} />
                                </div>
                            }
                            <div className="modal--qr__bg"></div>
                        </div>
                    </div>

                    <div className="wrapper--flip__card">
                        <div className="flip--card__inner" onClick={handleClickRotate}>
                            <div className="front">
                                <div className="wrapper--front__infor">
                                    <figure className="flip--card__img">
                                        <img src={avatar}
                                            onError={(e) => { e.target.src = defaultUser }}
                                            alt="" width="150px" />
                                        <figcaption><span>1026</span></figcaption>
                                    </figure>
                                    <div className="card--user__infor">
                                        <h1 className="card--title">
                                            {user_type === "2" ? ("") : ("")}
                                        </h1>
                                        {user_type === "2" ? (
                                            <>
                                                <div className="card--description">
                                                    <span className="card--items__title"><span></span> </span>
                                                    <p className="card--description__p">{teacher_course}</p>
                                                </div>
                                            </>) :
                                            (
                                                <>
                                                    <div className="card--description">
                                                        <span className="card--items__title"><span></span> </span>
                                                        <p className="card--description__p">{student_field_name}</p>
                                                    </div>
                                                    <div className="card--description">
                                                        <span className="card--items__title"><span></span></span>
                                                        <p className="card--description__p">{student_department_name}</p>
                                                    </div>
                                                </>)}

                                        <div className="card--description">
                                            <span className="card--items__title">{user_type === "2" ? ("") : ("")}<span></span></span>
                                            <p className="card--description__p">{username}</p>
                                        </div>
                                        <div className="card--description">
                                            <span className="card--items__title"><span></span></span>
                                            <p className="card--description__p">{fullname}</p>
                                        </div>
                                        <div className="card--description">
                                            <span className="card--items__title"><span></span></span>
                                            <p className="card--description__p">{date_of_birth}</p>
                                        </div>
                                        <div className="card--description">
                                            <span className="card--items__title"><span></span></span>
                                            <p className="card--description__p">{created}</p>
                                        </div>
                                        <div className="card--description">
                                            <span className="card--items__title"><span></span></span>
                                            <p className="card--description__p">{created}</p>
                                        </div>
                                    </div>
                                </div>

                                <div className="card--school__description">

                                    <div className="card--school__description__p"></div>
                                    <div className="card--school__description__p">{teacher_belong_to_name}</div>
                                </div>
                            </div>
                            <div className="back">
                                <div className="wrapper--back__infor">
                                    <div className="table--back__infor">
                                        <div className="table--back__infor__row">
                                            <div className="table--back__infor__items">
                                                <div className="table--row__header">
                                                    <p className="table--row__description_p"></p>
                                                </div>
                                                <div className="table--row__description">
                                                    <p className="table--row__description_p">
                                                        {admin_profile.all_group_name}
                                                    </p>
                                                    <p className="table--row__description_p">
                                                          {admin_profile.fullname}
                                                    </p>
                                                    <p className="table--row__description_p">
                                                        {admin_profile.address}
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="table--back__infor__row">
                                            <div className="table--back__infor__items">

                                                <div className="table--row__header">
                                                    {user_type === "2" ? ("") : ("")}
                                                    
                                                </div>
                                                <div className="table--row__description">
                                                    <p className="table--row__description_p">{address}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div >
            ) : <Login />
    )
}

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    user: state.auth.user,
    avatar: state.auth.avatar,
    user_information: state.auth.user_information,
    access: state.auth.access,
    admin_profile: state.auth.admin_profile
})

export default connect(mapStateToProps, { load_user, load_user_profile, load_user_profile_avatar, get_new_access_token, get_admin_profile })(Card)