import React from "react";
import { Link, NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout, load_user_profile } from '../actions/auth'
import { memo, useCallback, useEffect } from 'react';
import Login from "../containers/Login";
import logo from "../static/images/portal_logo.png";
const Navbar = ({
    logout,
    isAuthenticated,
    user_information,
    load_user_profile
}) => {
    //flag
    const $ = document.querySelector.bind(document);
    const $$ = document.querySelectorAll.bind(document);
    // handle check authenticated?
    const logout_user = useCallback(() => {
        logout();
    }, [])
    useEffect(() => {
        load_user_profile()
    }, [])
    // handle layout
    const handleCloseNavbar = useCallback(() => {
        const navbar = $('.wrapper--navbar__list');
        navbar.classList.remove('navbar--list__toggle');
    }, []);
    const handleOpenNavbar = useCallback(() => {
        const navbar = $('.wrapper--navbar__list');
        navbar.classList.add('navbar--list__toggle');
    }, []);
    const handleActive = useCallback(() => {
        const items = $$('.navbar--items')
        items.forEach(item => {
            item.onclick = function () {
                handleCloseNavbar();
            }
        })
    }, [])
    const itemsList = [
        {
            icon: <i className="fa-solid fa-house"></i>,
            path: "/home",
            path_name: "",
            user_type: ["2", "3"]
        },
        {
            icon: <i className="fa-regular fa-address-card"></i>,
            path: "/card",
            path_name: "",
            user_type: ["2", "3"]
        },
        {
            icon: <i className="fa-solid fa-book"></i>,
            path: "/classmanagement",
            path_name: "",
            user_type: ["2", "3"]
        },
        {
            icon: <i className="fa-regular fa-comment"></i>,
            path: "/chat",
            path_name: "",
            user_type: ["2", "3"]
        },
        {
            icon: <i className="fa-solid fa-user-group"></i>,
            path: "/studentsmanagement",
            path_name: "",
            user_type: ["2"]

        },
        {
            icon: <i className="fa-regular fa-object-group"></i>,
            path: "/groupmanagement",
            path_name: "",
            user_type: ["1"]
        },
        {
            icon: <i className="fa-solid fa-chalkboard-user"></i>,
            path: "/teachermanagement",
            path_name: "",
            user_type: ["1"]
        },
        {
            icon: <i className="fa-solid fa-circle-info"></i>,
            path: "/introduction",
            path_name: "",
            user_type: ["1"]
        }
    ]
    const OrtherList = [
        {
            icon: <i className="fa-solid fa-gear"></i>,
            path: "/passwordchange",
            path_name: "",
            user_type: ["1", "2", "3"]
        },
        {
            icon: <i className="fa-regular fa-circle-question"></i>,
            path: "/help",
            path_name: "",
            user_type: ["1", "2", "3"]
        },
    ]
    const Auth = [
        {
            icon: <i className="fa-solid fa-right-to-bracket"></i>,
            path: "/login",
            path_name: "",
        },
        {
            icon: <i className="fa-solid fa-user-plus"></i>,
            path: "/signup",
            path_name: "",
        }
    ]

    return (
        <header>
            <nav id="main-navbar" className="navbar">
                <div className="container-fluid">
                    <button className="btn navbar--list__btn" onClick={handleOpenNavbar} title="Navbar List">
                        <i className="navbar--icon fas fa-bars fa-lg"></i>
                    </button>
                    <div className="navbar--options__right">
                        {isAuthenticated ? (
                            <>
                                <span className="navbar--user__name"></span>
                                <button type="submit" href="#!" onClick={logout_user} className="btn narvbar--logout__btn separate">Logout</button>
                            </>
                        ) : (
                            <>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/login">Login</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/signup">Sign up</Link>
                                </li>
                            </>
                        )}
                    </div>
                    <div className="wrapper--navbar__list">

                        <div className="navbar--list__controller">

                            <div className="navbar--list__Logo">
                                <img src={logo} alt="School logo" className="" height="150px" width="100px" />
                            </div>
                            <button className="btn btn--close__mobile" onClick={handleCloseNavbar}><i className="fa-solid fa-circle-xmark"></i></button>
                        </div>
                        {isAuthenticated ? <>
                            <ul className="navbar--list"
                                onClick={handleActive}
                            >   {
                                    isAuthenticated ? (
                                        itemsList.map((item, index) => {
                                            for (let i of item.user_type) {
                                                if (i === user_information.user_type) {
                                                    return (<li key={index}>
                                                        <NavLink to={item.path} className={({ isActive }) => (isActive ? 'navbar--items active' : 'navbar--items')}>
                                                            <div className="navbar--items__content" >
                                                                {item.icon}
                                                                <span className="navbar--items__link" >{item.path_name}</span>
                                                            </div>
                                                        </NavLink>
                                                    </li>)
                                                }
                                            }
                                        }))
                                        :
                                        ("")
                                }
                                {
                                    isAuthenticated ?
                                        <li>
                                            <a className="navbar--items">
                                                <div className="navbar--items__content" onClick={logout_user}>
                                                    <i className="fa-solid fa-arrow-right-from-bracket"></i>
                                                    <span className="navbar--items__link" ></span>
                                                </div>
                                            </a>

                                        </li> : ("")
                                }
                            </ul>
                            <hr />
                            <ul className="navbar--list" >
                                {OrtherList.map((item, index) => (
                                    <li key={index}>
                                        <NavLink to={item.path} className={({ isActive }) => (isActive ? 'navbar--items active' : 'navbar--items')}>
                                            <div className="navbar--items__content" >
                                                {item.icon}
                                                <span className="navbar--items__link" >{item.path_name}</span>
                                            </div>
                                        </NavLink>
                                    </li>
                                ))}
                            </ul>
                        </> : (
                            <ul className="navbar--list top"
                                onClick={handleActive}>{
                                    Auth.map((item, index) => (
                                        <li key={index}>
                                            <NavLink to={item.path} className={({ isActive }) => (isActive ? 'navbar--items active' : 'navbar--items')}>
                                                <div className="navbar--items__content" >
                                                    {item.icon}
                                                    <span className="navbar--items__link" >{item.path_name}</span>
                                                </div>
                                            </NavLink>
                                        </li>
                                    ))}
                            </ul>
                        )}
                    </div>
                    <div className="navbar--list__bg" onClick={handleCloseNavbar}></div>
                </div>
            </nav>
        </header>
    );
};
const mapStateToProps = (state) => ({
    user_information: state.auth.user_information,
    isAuthenticated: state.auth.isAuthenticated
})

export default connect(mapStateToProps, { logout, load_user_profile })(memo(Navbar));