import React, { useState, useEffect } from "react";
import { connect } from 'react-redux';
import {
    update_student_profile,
    get_student_accounts_profile,
    load_class_school,
    signup_student,
    remove_student_account,
    get_students_avatar_profile,
    update_user_profile_avatar,
} from '../actions/auth';

import defaultUser from '../static/images/default_user.jpeg'

function StudentManagement({
    update_user_profile_avatar,
    get_students_avatar_profile,
    update_student_profile,
    res_mess,
    get_student_accounts_profile,
    student_list,
    load_class_school,
    classes_school,
    signup_student,
    remove_student_account,
    student_avatar_list,
}) {
    const $ = document.querySelector.bind(document);

    const [mounted, setMounted] = useState(false);
    const [edit, setEdit] = useState(false);
    const [avatarModal, setAvatarModal] = useState(false);
    const [add, setAdd] = useState(false);
    const [send, setSend] = useState(false);
    const [item, setItem] = useState({
        fullname: "",
        username: "",
        user: { username: "", email: "" },
        phone: "",
        address: "",
        date_of_birth: "",
        teacher_belong_to_name: "",
        teacher_sex: "",
        student_department_name: "",
        student_fullname_furigana: "",

    });
    const [errors, setErrors] = useState([]);
    const sex = ['', '', ''];

    const {
        fullname,
        phone,
        address,
        date_of_birth,
        teacher_sex,
        teacher_belong_to_id,
        student_field_name,
        student_department_name,
        student_class_name,
        student_fullname_furigana,
        student_post_num
    } = item;

    // const { email, username } = item.user;
    const [email, setEmail] = useState("");
    const [avatar, setAvatar] = useState();
    const [pic, setPic] = useState();

    function handleCloseModal() {
        const group = $(".group-modal");
        group.classList.remove("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200)
    };

    function handleOpenModal() {
        const group = $(".group-modal");
        group.classList.add("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200);
    };
    useEffect(() => {
        get_student_accounts_profile();
        load_class_school();
        get_students_avatar_profile();
    }, []);
    useEffect(() => {
        return () => {
            avatar && URL.revokeObjectURL(avatar.preview);
        }
    }, [avatar]);
    const handleAddItem = () => {
        handleOpenModal();
        setAvatarModal(false);
        setAdd(true);
        setEdit(true);
        setEmail("")
        setItem({
            fullname: "",
            username: "",
            phone: "",
            address: "",
            date_of_birth: "",
            teacher_belong_to_name: "",
            teacher_sex: "",
            student_class_name: "",
            student_field_name: "",
            student_department_name: "",
            student_class_name: "",
            student_fullname_furigana: "",
            student_post_num: ""
        });
    };
    const handleDeleteItem = (student) => {
        remove_student_account(student.user.id)
        setSend(true);
    }
    const handleUpdateItem = (item) => {
        setAvatarModal(false);
        setAdd(false);
        setEdit(true);
        setItem(item);
        setAvatar();
    }

    const handleItemOpenItem = (group) => {
        handleOpenModal();
        setEdit(false);
        setAdd(false)
        setItem(group);
        setEmail(group.user.email)
        student_avatar_list.map(i => {
            if (i.user === item.user.id) {
                setPic(i.avatar);
            };
        })
    };

    useEffect(() => {
        if (!add) {
            student_avatar_list.map(i => {
                if (i.user === item.user.id) {
                    setPic(i.avatar);
                };
            })
        } else {
            setPic()
        }
    }, [item]);
    const onChange = e => {
        setItem({ ...item, [e.target.name]: e.target.value });
    };
    const handleChangeEmail = e => {
        setEmail(e.target.value)
    }
    const handleChangeAvatar = e => {
        const file = e.target.files[0];
        file.preview = URL.createObjectURL(file);
        setAvatar(file);
        setPic()
    };
    const handleOpenChangeAvatarModal = (e) => {
        setAvatarModal(true);
        setEdit(true);
        setAdd(false);
        handleChangeAvatar(e);
    }

    const onSubmit = e => {
        e.preventDefault();
        if (add) {
            signup_student(
                fullname,
                phone,
                address,
                date_of_birth,
                teacher_sex,
                student_field_name,
                student_department_name,
                student_class_name,
                student_fullname_furigana,
                student_post_num,
                email)
            setSend(true)
        }
        else {
            if (avatarModal) {
                update_user_profile_avatar(avatar, item.user.id)
            }
            else {
                update_student_profile(fullname,
                    phone,
                    address,
                    date_of_birth,
                    teacher_sex,
                    student_field_name,
                    student_department_name,
                    student_class_name,
                    student_fullname_furigana,
                    student_post_num,
                    email,
                    item.user.id);
            }
        }
    }

    if (send) {
        setTimeout(() => {
            handleCloseModal()
        }, 200);
        setSend(!send)
    }
    return (
        <div className="wrapper--group__container">
            <div className="header--groupmanagement">
                <h1 className="container--title"></h1>
                <div className="groupmanagement__controller">
                    <i className="group--icon group--add fa-solid fa-plus" onClick={handleAddItem}></i>
                </div>
            </div>
            <ul className="teacher--list">
                {student_list.map((student, index) => {

                    return (
                        <li key={index} className="teacher--items"
                            onClick={
                                () => handleItemOpenItem(student)
                            }>
                            <div className="teacher--description">
                                <h3>{student.fullname}</h3>
                                <span><b>{student.teacher_belong_to_name}</b></span>
                                <span >ID<b>{student.user.username}</b></span>
                                <span><b>{student.date_of_birth}</b></span>
                                <span><b>{student.phone}</b></span>
                            </div>
                        </li>
                    )
                })}
            </ul>
            <div className={`group-modal ${mounted ? 'open' : ""}`}>
                <div className="modal--contain">
                    <div className="modal--contain__header">
                        {add ? <h1></h1> :
                            <h1>{edit ? '' : ''}</h1>}
                        <i className="group--icon group--remove fa-solid fa-xmark" onClick={handleCloseModal}></i>
                    </div>
                    {errors && <div className="wrapper--logger">
                    </div>}

                    {edit ? (
                        <form method="post" className="group-form" onSubmit={e => onSubmit(e)}>
                            <p style={{ color: "red" }}>
                                {res_mess && res_mess}
                            </p>
                            {avatarModal ?
                                (
                                    <div className="wrapper--input">
                                        <div style={{ height: "110px", }}>
                                            {
                                                avatar ? (
                                                    <img src={avatar.preview}
                                                        style={{ width: "80px", position: "absolute" }}
                                                    />
                                                ) :
                                                    pic &&
                                                    <img src={pic}
                                                        style={{ width: "80px", position: "absolute" }}
                                                    />
                                            }
                                        </div>

                                        <label className="avt-input">
                                            <input
                                                type="file"
                                                placeholder="ok"
                                                onChange={handleChangeAvatar}
                                                // value={teacher_avt}
                                                style={{ flexShrink: 0 }}
                                            />
                                            
                                        </label>
                                    </div>
                                ) : (
                                    <>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="student_class_name">
                                            </label>
                                            <select onChange={(e) => onChange(e)} id="student_class_name" className="teacher_belong_to_id" name="student_class_name" defaultValue={student_class_name}>
                                                <option key="select" value={""} ></option >
                                                {classes_school.map((item, index) => {
                                                    return (
                                                        <option name="teacher_belong_to_id" key={index} value={item.class_name}>{item.class_name}</option >
                                                    )
                                                }
                                                )}
                                            </select>
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="fullname">
                                            </label>
                                            <input className="input__fields"
                                                type='text'
                                                id="fullname"
                                                name="fullname"
                                                placeholder=""
                                                value={fullname}
                                                onChange={e => onChange(e)}
                                                maxLength={255}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="student_fullname_furigana">
                                            </label>
                                            <input className="input__fields"
                                                type='text'
                                                id="student_fullname_furigana"
                                                name="student_fullname_furigana"
                                                placeholder=""
                                                value={student_fullname_furigana}
                                                onChange={e => onChange(e)}
                                                maxLength={255}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="phone">
                                            </label>
                                            <input className="input__fields"
                                                type='tel'
                                                id="phone"
                                                name="phone"
                                                placeholder=""
                                                value={phone}
                                                onChange={e => onChange(e)}
                                                maxLength={255}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="address">
                                            </label>
                                            <input className="input__fields"
                                                type='text'
                                                id="address"
                                                name="address"
                                                placeholder=""
                                                value={address}
                                                onChange={e => onChange(e)}
                                                required
                                                maxLength={255}
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="email">
                                            </label>
                                            <input className="input__fields"
                                                type='email'
                                                id="email"
                                                name="email"
                                                placeholder=""
                                                value={email}
                                                onChange={e => handleChangeEmail(e)}
                                                maxLength={255}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="date_of_birth">
                                            </label>
                                            <input className="input__fields"
                                                type='date'
                                                id="date_of_birth"
                                                name="date_of_birth"
                                                placeholder=""
                                                value={date_of_birth}
                                                onChange={e => onChange(e)}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="student_field_name">
                                            </label>
                                            <input className="input__fields"
                                                type='text'
                                                id="student_field_name"
                                                name="student_field_name"
                                                value={student_field_name}
                                                onChange={(e) => onChange(e)}
                                                maxLength={255}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="student_department_name">
                                            </label>
                                            <input className="input__fields"
                                                type='text'
                                                id="student_department_name"
                                                name="student_department_name"
                                                placeholder=""
                                                value={student_department_name}
                                                onChange={e => onChange(e)}
                                                maxLength={255}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input">
                                            <label className="input--label"
                                                htmlFor="student_post_num">
                                            </label>
                                            <input className="input__fields"
                                                type='text'
                                                id="student_post_num"
                                                name="student_post_num"
                                                placeholder=""
                                                value={student_post_num}
                                                onChange={e => onChange(e)}
                                                maxLength={255}
                                                required
                                            />
                                        </div>
                                        <div className="wrapper--input radio_sex">
                                            <label className="input--label"
                                                htmlFor="teacher_sex">
                                            </label>
                                            <input className="input__fields"
                                                type='radio'
                                                id="teacher_sex"
                                                name="teacher_sex"
                                                value="2"
                                                checked={teacher_sex === "2"}
                                                onChange={e => onChange(e)}
                                                required
                                            />
                                            <p>
                                                
                                            </p>
                                            <input className="input__fields"
                                                type='radio'
                                                id="teacher_sex"
                                                name="teacher_sex"
                                                value="1"
                                                checked={teacher_sex === "1"}
                                                onChange={e => onChange(e)}
                                                required
                                            />
                                            <p>

                                                
                                            </p>
                                            <input className="input__fields"
                                                type='radio'
                                                id="teacher_sex"
                                                name="teacher_sex"
                                                value="3"
                                                checked={teacher_sex === "3"}
                                                onChange={e => onChange(e)}
                                                required
                                            />
                                            <p>
                                                
                                            </p>
                                        </div>
                                    </>)
                            }
                            <button className="btn group--btn primary" type="submit">{edit ? "" : ""}</button>
                        </form>
                    ) : (
                        <div className="teacher--infor__container">
                            <div className="user--header">
                                <div className="user--description">
                                    {
                                        <div className="user__avatar" onClick={e => handleOpenChangeAvatarModal(e)}>
                                            <>
                                                <img
                                                    src={pic}
                                                    style={{ width: "80px" }}
                                                    alt="user image"
                                                    onError={(e) => { e.target.src = defaultUser }}
                                                />
                                                <i className="user__avatar--icon fa-solid fa-pen-to-square"></i>
                                            </>
                                        </div>

                                    }
                                    <h2 className="teacher--infor">
                                        <span>{item.fullname}</span><br />
                                        <span>{item.user.username}</span>
                                    </h2>
                                </div>
                                <div className="user--controller">
                                    <button className="user--controller__edit" onClick={() => handleUpdateItem(item)} type="submit"></button>
                                    <button className="user--controller__remove" type="submit" onClick={() => handleDeleteItem(item)}></button>
                                </div>
                            </div>
                            <ul className="teacher--infor__list">
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.teacher_belong_to_name}</p>
                                </li>

                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p >{item.user.email}</p>
                                </li>

                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.phone}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.student_class_name}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.address}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.date_of_birth}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.student_field_name}</p>
                                </li>
                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{item.student_department_name}</p>
                                </li>

                                <li className="teacher--infor__item">
                                    <span><span></span></span>
                                    <p>{sex[item.teacher_sex - 1]}</p>
                                </li>
                            </ul>
                        </div>
                    )}
                </div>
                <div className="modal--group__bg" onClick={handleCloseModal}></div>
            </div >
        </div >
    )
}
const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    classes_school: state.auth.classes_school,
    student_list: state.auth.student_list,
    res_mess: state.auth.res_mess,
    student_avatar_list: state.auth.student_avatar_list
})
export default connect(mapStateToProps, {
    get_student_accounts_profile,
    load_class_school,
    signup_student,
    remove_student_account,
    update_student_profile,
    get_students_avatar_profile,
    update_user_profile_avatar
})(StudentManagement);