import {
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    USER_LOADED_SUCCESS,
    USER_LOADED_FAIL,
    AUTHENTICATED_FAIL,
    AUTHENTICATED_SUCCESS,
    PASSWORD_RESET_SUCCESS,
    PASSWORD_RESET_FAIL,
    PASSWORD_RESET_CONFIRM_SUCCESS,
    PASSWORD_RESET_CONFIRM_FAIL,
    SIGNUP_SUCCESS,
    SIGNUP_FAIL,
    ACTIVATION_SUCCESS,
    ACTIVATION_FAIL,
    LOAD_USER_PROFILE_SUCCESS,
    LOAD_USER_PROFILE_FAIL,
    UPDATE_USER_PROFILE_SUCCESS,
    UPDATE_USER_PROFILE_FAIL,
    LOAD_USER_PROFILE_AVATAR_SUCCESS,
    LOAD_USER_PROFILE_AVATAR_FAIL,
    UPDATE_USER_PROFILE_AVATAR_SUCCESS,
    UPDATE_USER_PROFILE_AVATAR_FAIL,
    LOAD_GROUPS_SCHOOL_SUCCESS,
    LOAD_GROUPS_SCHOOL_FAIL,
    CREATE_GROUP_SCHOOL_SUCCESS,
    CREATE_GROUP_SCHOOL_FAIL,
    UPDATE_GROUP_SCHOOL_SUCCESS,
    UPDATE_GROUP_SCHOOL_FAIL,
    REMOVE_GROUP_SCHOOL_SUCCESS,
    REMOVE_GROUP_SCHOOL_FAIL,
    LOAD_TEACHERS_ACCOUNT_SUCCESS,
    LOAD_TEACHERS_ACCOUNT_FAIL,
    CREATE_TEACHERS_ACCOUNT_SUCCESS,
    CREATE_TEACHERS_ACCOUNT_FAIL,
    REMOVE_TEACHERS_ACCOUNT_SUCCESS,
    REMOVE_TEACHERS_ACCOUNT_FAIL,
    LOAD_TEACHERS_ACCOUNT_BASE_SUCCESS,
    LOAD_TEACHERS_ACCOUNT_BASE_FAIL,
    LOAD_TEACHERS_AVATAR_SUCCESS,
    LOAD_TEACHERS_AVATAR_FAIL,
    UPDATE_TEACHER_PROFILE_SUCCESS,
    UPDATE_TEACHER_PROFILE_FAIL,
    UPDATE_ACCESS_TOKEN_SUCCESS,
    UPDATE_ACCESS_TOKEN_FAIL,
    CHANGE_PASSWORD_SUCCESS,
    CHANGE_PASSWORD_FAIL,
    LOAD_ADMIN_PROFILE_SUCCESS,
    LOAD_ADMIN_PROFILE_FAIL,
    LOAD_CLASS_SCHOOL_SUCCESS,
    LOAD_CLASS_SCHOOL_FAIL,
    CREATE_CLASS_SCHOOL_SUCCESS,
    CREATE_CLASS_SCHOOL_FAIL,
    UPDATE_CLASS_SCHOOL_SUCCESS,
    UPDATE_CLASS_SCHOOL_FAIL,
    // REMOVE_CLASS_SCHOOL_SUCCESS,
    // REMOVE_CLASS_SCHOOL_FAIL,
    LOAD_STUDENTS_ACCOUNT_SUCCESS,
    // LOAD_STUDENTS_ACCOUNT_FAIL,
    LOAD_STUDENTS_AVATAR_SUCCESS,
    // LOAD_STUDENTS_AVATAR_FAIL,
    CREATE_STUDENTS_ACCOUNT_SUCCESS,
    CREATE_STUDENTS_ACCOUNT_FAIL,
    REMOVE_STUDENTS_ACCOUNT_SUCCESS,
    REMOVE_STUDENTS_ACCOUNT_FAIL,
    UPDATE_STUDENT_PROFILE_SUCCESS,
    UPDATE_STUDENT_PROFILE_FAIL,
    LOAD_POST_SUCCESS,
    LOAD_POST_FAIL,
    LOAD_POSTERS_SUCCESS,
    LOAD_POSTERS_FAIL,
    CREATE_POST_SUCCESS,
    CREATE_POST_FAIL,
    UPDATE_POST_SUCCESS,
    UPDATE_POST_FAIL,
    REMOVE_POST_SUCCESS,
    REMOVE_POST_FAIL,
    LOGOUT
} from '../actions/type';

const initialState = {
    access: localStorage.getItem('access'),
    refresh: localStorage.getItem('refresh'),
    isAuthenticated: null,
    user: {
        username: "",
        email: "",
        created_at: ""
    },
    user_information: {
        username: '',
        date_of_birth: '',
        phone: '',
        address: '',
        group_name: '',
        teacher_librar: '',
        user_type: ''
    },
    avatar: null,
    school_groups: [],
    school_group: {
        group_name: "",
        group_phone: "",
        group_id: "",
        group_address: "",
        abbreviation: ""
    },
    classes_school: [],
    teacher_list: [],
    teacher_accounts: [],
    teacher_avt_lst: [],
    res_mess: null,
    admin_profile: {
        fullname: "",
        address: "",
        phone: "",
        all_group_name: ""
    },
    success: null,
    student_list: [],
    student_avatar_list: [],
    posts: [],
    posters: [],
    visible: 3,
    isLoad: true,
    updatePost: {},
}

export default function (state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case AUTHENTICATED_SUCCESS:
            return {
                ...state,
                isAuthenticated: true,
                success: null,
            };
        case LOGIN_SUCCESS:
            localStorage.setItem('refresh', payload.refresh)
            localStorage.setItem('access', payload.access);
            return {
                ...state,
                res_mess: null,
                isAuthenticated: true,
                access: payload.access,
                refresh: payload.refresh
            };
        case SIGNUP_SUCCESS:
            return {
                ...state,
                isAuthenticated: false,
                res_mess: null
            };
        case USER_LOADED_SUCCESS:
            return {
                ...state,
                user: payload
            };
        case AUTHENTICATED_FAIL:
            return {
                ...state,
                isAuthenticated: false
            };
        case USER_LOADED_FAIL:
            return {
                ...state,
                user: {
                    username: "",
                    email: "",
                    created_at: ""
                },
            };
        case LOGIN_FAIL:
        case SIGNUP_FAIL:
        case LOGOUT:
            localStorage.removeItem('access');
            localStorage.removeItem('refresh');
            return {
                ...state,
                access: null,
                refresh: null,
                isAuthenticated: false,
                user: {
                    username: "",
                    email: "",
                    created_at: ""
                },
                user_information: {
                    username: '',
                    date_of_birth: '',
                    phone: '',
                    address: '',
                    group_name: '',
                    teacher_librar: '',
                    user_type: ''
                },
                res_mess: payload
            };

        case LOAD_USER_PROFILE_AVATAR_SUCCESS:
            return {
                ...state,
                avatar: payload.avatar
            }
        case LOAD_USER_PROFILE_AVATAR_FAIL:
            return {
                ...state,
                avatar: null
            }

        case LOAD_TEACHERS_AVATAR_SUCCESS:
            return {
                ...state,
                teacher_avt_lst: [
                    ...payload
                ]
            }


        case LOAD_USER_PROFILE_SUCCESS:
            return {
                ...state,
                user_information: {
                    ...payload
                }
            };
        case LOAD_POST_SUCCESS:
            return {
                ...state,
                posts: [
                    ...payload.data
                ]
            };
        case UPDATE_USER_PROFILE_SUCCESS:
        case LOAD_POST_FAIL:
            return { ...state };

        case LOAD_USER_PROFILE_FAIL:
            return {
                ...state,
                user_information: {
                    username: '',
                    date_of_birth: '',
                    phone: '',
                    address: '',
                    group_name: '',
                    teacher_librar: '',
                    user_type: ''
                }
            };
        case UPDATE_USER_PROFILE_AVATAR_FAIL:
            return {
                ...state,
                avatar: null
            }
        case UPDATE_CLASS_SCHOOL_SUCCESS:
        case CREATE_CLASS_SCHOOL_SUCCESS:
            return {
                ...state,
                res_mess: null,
                success: true
            }
        case REMOVE_POST_SUCCESS:
        case REMOVE_POST_FAIL:
        case CREATE_POST_SUCCESS:
        case CREATE_POST_FAIL:
        case UPDATE_USER_PROFILE_AVATAR_SUCCESS:
        case REMOVE_GROUP_SCHOOL_SUCCESS:
        case UPDATE_TEACHER_PROFILE_SUCCESS:
        case CREATE_TEACHERS_ACCOUNT_FAIL:
        case UPDATE_USER_PROFILE_FAIL:
        case UPDATE_GROUP_SCHOOL_FAIL:
        case PASSWORD_RESET_SUCCESS:
        case PASSWORD_RESET_FAIL:
        case PASSWORD_RESET_CONFIRM_SUCCESS:
        case PASSWORD_RESET_CONFIRM_FAIL:
        case ACTIVATION_SUCCESS:
        case REMOVE_GROUP_SCHOOL_FAIL:
        case ACTIVATION_FAIL:
        case UPDATE_TEACHER_PROFILE_FAIL:
        case UPDATE_ACCESS_TOKEN_FAIL:
        case LOAD_TEACHERS_AVATAR_FAIL:
            return {
                ...state
            };
        case LOAD_GROUPS_SCHOOL_SUCCESS:
            return {
                ...state,
                school_groups: payload
            };
        case LOAD_GROUPS_SCHOOL_FAIL:
            return {
                ...state,
                school_groups: []
            };
        case CREATE_GROUP_SCHOOL_SUCCESS:
            return {
                ...state,
                school_group: {
                    ...payload
                }
            };
        case LOAD_ADMIN_PROFILE_SUCCESS:
            return {
                ...state,
                admin_profile: {
                    ...payload
                }
            }
        case LOAD_ADMIN_PROFILE_FAIL:
            return {
                ...state,
                admin_profile: {
                    fullname: "",
                    address: "",
                    phone: "",
                    all_group_name: ""
                }
            }
        case CREATE_GROUP_SCHOOL_FAIL:
            return {
                ...state,
                school_group: {
                    group_name: "",
                    group_phone: "",
                    group_id: "",
                    group_address: "",
                    abbreviation: ""
                }
            };
        case LOAD_CLASS_SCHOOL_SUCCESS:
            return {
                ...state,
                classes_school: [...payload]
            }
        case UPDATE_GROUP_SCHOOL_SUCCESS:
            return {
                ...state,
                school_group: payload
            }
        case REMOVE_TEACHERS_ACCOUNT_SUCCESS:
        case CREATE_TEACHERS_ACCOUNT_SUCCESS:
        case LOAD_CLASS_SCHOOL_FAIL:
        case UPDATE_POST_FAIL:
            return {
                ...state,
            };

        case UPDATE_POST_SUCCESS:
            return {
                ...state,
                posts: [...payload]
            }
        case REMOVE_TEACHERS_ACCOUNT_FAIL:
            return {
                ...state,
                teacher_list: [
                    ...payload
                ]
            }
        case LOAD_TEACHERS_ACCOUNT_SUCCESS:
            return {
                ...state,
                teacher_list: [
                    ...payload
                ]
            }
        case LOAD_TEACHERS_ACCOUNT_FAIL:
            return {
                ...state,
                teacher_list: []
            }
        case LOAD_STUDENTS_ACCOUNT_SUCCESS:
            return {
                ...state,
                student_list: [...payload],
            }
        case LOAD_TEACHERS_ACCOUNT_BASE_SUCCESS:
            return {
                ...state,
                teacher_accounts: [
                    ...payload
                ]
            };
        case CHANGE_PASSWORD_SUCCESS:
            return {
                ...state,
                success: true,
            }

        case UPDATE_STUDENT_PROFILE_SUCCESS:
            return {
                ...state,
                res_mess: null
            }
        case LOAD_STUDENTS_AVATAR_SUCCESS:
            return {
                ...state,
                student_avatar_list: [...payload]
            }
        case CREATE_STUDENTS_ACCOUNT_SUCCESS:
        case CREATE_STUDENTS_ACCOUNT_FAIL:
        case REMOVE_STUDENTS_ACCOUNT_SUCCESS:
        case REMOVE_STUDENTS_ACCOUNT_FAIL:
        case UPDATE_STUDENT_PROFILE_FAIL:
        case UPDATE_CLASS_SCHOOL_FAIL:
        case CREATE_CLASS_SCHOOL_FAIL:
        case CHANGE_PASSWORD_FAIL:
            return {
                ...state,
                res_mess: payload,
            }
        case LOAD_TEACHERS_ACCOUNT_BASE_FAIL:
            return {
                ...state,
                teacher_accounts: []
            };
        case UPDATE_ACCESS_TOKEN_SUCCESS:
            localStorage.setItem('access', payload.access);
            return {
                ...state,
                access: payload.access
            };
        case LOAD_POSTERS_SUCCESS:
            return {
                ...state,
                posters: [
                    ...payload.data
                ],
                isLoad: payload.isLoad
            };
        // case LOAD_POSTERS_FAIL:
        //     return {
        //         ...state,
        //         posters: []
        //     };
        default: {
            return state;
        };
    }
}