import React, { useState, useEffect } from "react";
import { connect } from 'react-redux';
import { load_school_group, create_school_group, update_school_group, remove_school_group } from '../actions/auth';

function Groupmanagement({ load_school_group, school_groups, create_school_group, school_group, update_school_group, remove_school_group }) {
    const $ = document.querySelector.bind(document);
    const [mounted, setMounted] = useState(false);
    const [edit, setEdit] = useState(false);
    const [send, setSend] = useState(false);

    function handleCloseModal() {
        const group = $(".group-modal");
        group.classList.remove("open");
        setTimeout(() => {
            setMounted(!mounted)
            setSend(false)
        }, 200)
    }

    function handleOpenModal() {
        const group = $(".group-modal");
        group.classList.add("open");
        setTimeout(() => {
            setMounted(!mounted)
        }, 200);
        setItem({
            group_name: "",
            group_phone: "",
            group_address: "",
            group_id: "",
            sign: ""
        });
        setEdit(false);
    }

    useEffect(() => {
        load_school_group()
    }, [])

    const [item, setItem] = useState(school_group)
    const { group_name, group_phone, group_address, group_id, sign } = item;

    const handleItemOpenItem = (group) => {
        handleOpenModal()
        setEdit(true)
        setItem(group)
    }
    const onChange = e => {
        setItem({ ...item, [e.target.name]: e.target.value });
    }

    const handleRemove = () => {
        remove_school_group(item.id)
        setSend(true)
    }
    const onSubmit = (e) => {
        e.preventDefault()
        if (!edit) {
            create_school_group(group_name, group_phone, group_address, group_id, sign)
        }
        else {
            update_school_group(item.id, group_name, group_phone, group_address, group_id, sign)
        }
        setSend(true)
    }
    if (send) {
        handleCloseModal()
        setSend(false)
    }

    return (
        <div className="wrapper--group__container">

            <div className="header--groupmanagement">
                <h1 className="container--title"></h1>
                <div className="groupmanagement__controller">
                    <i className="group--icon group--add fa-solid fa-plus" onClick={handleOpenModal}></i>
                </div>
            </div>

            <ul className="group--list">
                {school_groups.map((group, index) => {
                    return (
                        <li key={index} className="group--items" onClick={() => handleItemOpenItem(group)}>
                            <h3>{group.group_name}</h3>
                            <div className="group--description">
                                <span>ID<b>{group.group_id}</b></span>
                                <span><b>{group.group_phone}</b></span>
                                <span><b>{group.group_address}</b></span>
                                <span><b>{group.sign}</b></span>
                            </div>
                        </li>
                    )
                })}
            </ul>
            <div className={`group-modal ${mounted ? 'open' : ""}`}>
                <div className="modal--contain">
                    <div className="modal--contain__header">
                        <h1>{edit ? "" : ""}</h1>
                        <i className="group--icon group--remove fa-solid fa-xmark" onClick={handleCloseModal}></i>
                    </div>
                    {edit ? (<button className="group--btn__remove" onClick={handleRemove}></button>) : ""}
                    <form method="post" className="group-form" onSubmit={e => onSubmit(e)}>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="group_id">ID
                            </label>
                            <input className="input__fields"
                                type='text'
                                id="group_pid"
                                name="group_id"
                                placeholder="ID"
                                value={group_id}
                                onChange={e => onChange(e)}
                                maxLength={255}

                                required
                            />
                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="group_name"></label>
                            <input className="input__fields"
                                type='text'
                                id="group_name"
                                placeholder=""
                                name="group_name"
                                value={group_name}
                                onChange={e => onChange(e)}
                                maxLength={255}
                                required
                            />
                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="group_address"></label>
                            <input className="input__fields"
                                type='text'
                                id="group_address"
                                name="group_address"
                                placeholder=""
                                value={group_address}
                                maxLength={255}
                                onChange={e => onChange(e)}
                                required
                            />
                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="group_phone"></label>
                            <input className="input__fields"
                                type='tel'
                                id="group_phone"
                                name="group_phone"
                                placeholder=""
                                value={group_phone}
                                onChange={e => onChange(e)}
                                maxLength={20}
                                required
                            />
                        </div>
                        <div className="wrapper--input">
                            <label className="input--label"
                                htmlFor="sign"></label>
                            <input className="input__fields"
                                type='text'
                                id="sign"
                                name="sign"
                                placeholder=""
                                value={sign}
                                maxLength={2}
                                onChange={e => onChange(e)}
                                required
                            />
                        </div>
                        <button className="btn group--btn primary" type="submit">{edit ? "" : ""}</button>
                    </form>
                </div>
                <div className="modal--group__bg" onClick={handleCloseModal}></div>
            </div >
        </div >
    )
}
const mapStateToProps = state => ({
    school_groups: state.auth.school_groups,
    school_group: state.auth.school_group
})

export default connect(mapStateToProps, { load_school_group, create_school_group, update_school_group, remove_school_group })(Groupmanagement);